/* =============================================================================
   SIDECAR COMPONENTS
   Minimalist, bottleneck-focused design
   ============================================================================= */

/* Status Bar - Modern minimal design */
.sidecar-status-bar {
  padding: 6px 12px;
  background: #ffffff;
  border-bottom: 1px solid rgba(11, 18, 32, 0.08);
  display: flex;
  flex-direction: column;
  gap: 4px;
}

.sidecar-status-bar-title-row {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 8px;
}

.sidecar-status-bar-title {
  font-size: 7px;
  font-weight: 600;
  color: rgba(91, 102, 122, 0.95);
  text-transform: uppercase;
  letter-spacing: 0.04em;
}

.sidecar-status-bar-context {
  font-size: 8px;
  font-weight: 400;
  color: rgba(91, 102, 122, 0.8);
  white-space: nowrap;
}

.sidecar-status-bar-progress-row {
  display: flex;
  align-items: center;
  gap: 6px;
}

.sidecar-status-bar-track-wrapper {
  flex: 1;
  min-width: 0;
}

.sidecar-status-bar-track {
  position: relative;
  height: 3px;
  border-radius: 2px;
  /* Mono grey gradient - subtle, modern */
  background: linear-gradient(90deg, 
    rgba(11, 18, 32, 0.08) 0%, 
    rgba(11, 18, 32, 0.12) 50%,
    rgba(11, 18, 32, 0.06) 100%
  );
}

/* Progress fill - darker grey to show completion */
.sidecar-status-bar-progress {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  background: rgba(11, 18, 32, 0.35);
  border-radius: 2px;
  transition: width 0.3s ease;
}

/* Current position marker */
.sidecar-status-bar-marker {
  position: absolute;
  top: -3px;
  width: 9px;
  height: 9px;
  border-radius: 50%;
  transform: translateX(-50%);
  background: #0b1220;
  border: 2px solid #ffffff;
  box-shadow: 0 1px 3px rgba(2, 6, 23, 0.2);
  z-index: 2;
}

/* Percentage label */
.sidecar-status-bar-percent {
  font-size: 9px;
  font-weight: 600;
  color: #0b1220;
  white-space: nowrap;
}

/* Bottleneck Card - Core UI */
/* Mobius Greeting */
.sidecar-greeting {
  display: flex;
  flex-direction: column;
  gap: 2px;
  padding-bottom: 8px;
  margin-bottom: 6px;
  border-bottom: 1px solid rgba(11, 18, 32, 0.06);
