}

.sidecar-greeting-subtext {
  font-size: 10px;
  font-weight: 400;
  color: rgba(91, 102, 122, 0.6);
}

.sidecar-bottleneck-card {
  padding: 0;
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.sidecar-bottleneck-card--clear {
  padding: 6px 0;
}

.sidecar-bottleneck-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-bottom: 6px;
  border-bottom: 1px solid rgba(11, 18, 32, 0.08);
}

.sidecar-bottleneck-count {
  font-size: 11px;
  font-weight: 500;
  color: #0b1220;
}

.sidecar-bottleneck-hint {
  font-size: 9px;
  font-weight: 400;
  color: rgba(91, 102, 122, 0.6);
}

.sidecar-bottleneck-item {
  background: #ffffff;
  border: 1px solid rgba(11, 18, 32, 0.08);
  border-radius: 6px;
  padding: 6px 8px;
  display: flex;
  box-shadow: none;
  flex-direction: column;
  gap: 4px;
}

.sidecar-bottleneck-question-row {
  display: flex;
  align-items: flex-start;
  gap: 4px;
}

.sidecar-bottleneck-question-row::before {
  content: '';
  display: inline-block;
  width: 4px;
  height: 4px;
  min-width: 4px;
  background: #3b82f6;
  border-radius: 50%;
  margin-top: 4px;
  flex-shrink: 0;
}

.sidecar-bottleneck-checkbox {
  width: 14px;
  height: 14px;
  cursor: pointer;
  margin-top: 1px;
}

.sidecar-bottleneck-question {
  flex: 1;
  font-size: 10px;
  font-weight: 500;
  color: #0b1220;
  line-height: 1.25;
}

.sidecar-source-badge {
  font-size: 10px;
  opacity: 0.7;
}

.sidecar-conflict-badge {
  font-size: 12px;
}

.sidecar-bottleneck-answers {
  display: flex;
  gap: 10px;
  flex-wrap: wrap;
  margin-top: 4px;
  padding: 4px 0;
  align-items: center;
  border-top: 1px solid rgba(11, 18, 32, 0.04);
}

#mobius-os-sidebar .sidecar-bottleneck-answers button.sidecar-answer-btn,
.sidecar-answer-btn {
