}

.sidecar-bottleneck-note-send {
  position: absolute;
  right: 0;
  width: 22px;
  height: 22px;
  border-radius: 6px;
  border: 1px solid rgba(11, 18, 32, 0.12);
  background: rgba(11, 18, 32, 0.03);
