}

/* Inline button (Edit, small actions) */
.sidecar-inline-btn {
  height: 22px;
  padding: 0 8px;
  border-radius: 8px;
  border: 1px solid rgba(11, 18, 32, 0.12);
  background: rgba(11, 18, 32, 0.03);
  cursor: pointer;
  font-size: 9px;
  font-weight: 600;
  color: #0b1220;
}

.sidecar-inline-btn:hover {
  background: rgba(11, 18, 32, 0.06);
}

/* Icon-only button (pencil edit, etc) - matches Mini */
.sidecar-icon-btn {
  width: 22px;
  height: 22px;
  border-radius: 8px;
  border: 1px solid rgba(11, 18, 32, 0.12);
  background: rgba(11, 18, 32, 0.03);
  cursor: pointer;
  display: grid;
  place-items: center;
  padding: 0;
}

.sidecar-icon-btn:hover {
  background: rgba(11, 18, 32, 0.06);
}

.sidecar-icon-btn svg {
  width: 12px;
  height: 12px;
  fill: rgba(11, 18, 32, 0.80);
}

.sidecar-bottleneck-bulk {
  padding-top: 12px;
  border-top: 1px solid #e5e7eb;
}

.sidecar-bottleneck-bulk-btn {
  width: 100%;
  padding: 8px 12px;
  font-size: 10px;
  font-weight: 600;
  background: rgba(11, 18, 32, 0.03);
  color: #0b1220;
  border: 1px solid rgba(11, 18, 32, 0.12);
  border-radius: 10px;
  cursor: pointer;
  transition: all 0.15s ease;
}

.sidecar-bottleneck-bulk-btn:hover:not(:disabled) {
  background: rgba(11, 18, 32, 0.06);
}

.sidecar-bottleneck-bulk-btn:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

.sidecar-bottleneck-clear {
  display: flex;
  align-items: center;
  gap: 10px;
  padding: 10px 12px;
  background: rgba(34, 197, 94, 0.08);
  border: 1px solid rgba(34, 197, 94, 0.15);
  border-radius: 10px;
}

.sidecar-bottleneck-clear-icon {
  width: 24px;
  height: 24px;
  background: #22c55e;
  color: white;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 12px;
  font-weight: bold;
}

.sidecar-bottleneck-clear-text {
  font-size: 11px;
  color: #166534;
  font-weight: 500;
}

/* Context Expander */
/* Context Expander - matches Mini row styling */
.sidecar-context-expander {
  border-top: 1px solid rgba(11, 18, 32, 0.08);
  margin-top: 8px;
}

.sidecar-context-expander-toggle {
  width: 100%;
  padding: 6px 0;
  background: none;
  border: none;
  display: flex;
  align-items: center;
  gap: 4px;
  cursor: pointer;
  font-size: 10px;
  font-weight: 400;
  color: rgba(91, 102, 122, 0.95);
  transition: background 0.15s ease;
}

.sidecar-context-expander-toggle:hover {
  background: rgba(11, 18, 32, 0.02);
}

.sidecar-context-expander-arrow {
  font-size: 8px;
  color: rgba(91, 102, 122, 0.4);
  transition: transform 0.15s ease;
}

.sidecar-context-expander-toggle[aria-expanded="true"] .sidecar-context-expander-arrow {
  transform: none;
}

.sidecar-context-expander-label {
  font-weight: 400;
  color: rgba(91, 102, 122, 0.7);
}

.sidecar-context-expander-content {
  padding: 0 0 8px;
}

.sidecar-section-title {
  font-size: 7px;
  font-weight: 500;
  color: rgba(91, 102, 122, 0.6);
  text-transform: uppercase;
  letter-spacing: 0.04em;
  margin-bottom: 4px;
}

/* Resolution Plan Section */
.sidecar-plan-section {
  margin-bottom: 10px;
  padding-bottom: 8px;
  border-bottom: 1px solid rgba(11, 18, 32, 0.06);
}

.sidecar-plan-summary {
  margin-bottom: 6px;
}

.sidecar-plan-factors {
  display: flex;
  flex-direction: column;
  gap: 4px;
}

.sidecar-plan-factor {
  display: flex;
  align-items: center;
  gap: 6px;
  font-size: 9px;
}

.sidecar-plan-factor-label {
  flex: 1;
  color: rgba(91, 102, 122, 0.85);
  font-weight: 400;
}

.sidecar-plan-factor-progress {
  font-size: 8px;
  color: rgba(91, 102, 122, 0.6);
  font-weight: 400;
}

.sidecar-plan-factor-bar {
  width: 40px;
  height: 3px;
  background: rgba(11, 18, 32, 0.08);
  border-radius: 2px;
  overflow: hidden;
}

.sidecar-plan-factor-fill {
  height: 100%;
  background: rgba(11, 18, 32, 0.35);
  border-radius: 2px;
  transition: width 0.3s ease;
}

.sidecar-plan-current {
  display: flex;
  align-items: flex-start;
  gap: 4px;
  padding: 4px 6px;
  background: rgba(11, 18, 32, 0.03);
  border-radius: 6px;
  margin-top: 6px;
}

.sidecar-plan-current-label {
  font-size: 8px;
  font-weight: 400;
  color: rgba(91, 102, 122, 0.6);
  flex-shrink: 0;
}

.sidecar-plan-current-text {
  font-size: 9px;
  font-weight: 400;
  color: rgba(91, 102, 122, 0.85);
  line-height: 1.3;
}

.sidecar-plan-actions {
  font-size: 8px;
  font-weight: 400;
  color: rgba(37, 99, 235, 0.8);
  margin-top: 4px;
}

/* Milestones - matches Mini compact styling */
.sidecar-milestones-list {
  display: flex;
  flex-direction: column;
  gap: 4px;
}

.sidecar-milestone {
  background: rgba(11, 18, 32, 0.02);
  border: 1px solid rgba(11, 18, 32, 0.06);
  border-radius: 8px;
  overflow: hidden;
}

.sidecar-milestone-header {
  display: flex;
  align-items: center;
  gap: 6px;
  padding: 6px 10px;
}

.sidecar-milestone-icon {
  width: 20px;
  height: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 12px;
}

.sidecar-milestone--complete .sidecar-milestone-icon {
  color: #22c55e;
}

.sidecar-milestone--in_progress .sidecar-milestone-icon {
  color: #f59e0b;
}

.sidecar-milestone--blocked .sidecar-milestone-icon {
  color: #ef4444;
}

.sidecar-milestone--pending .sidecar-milestone-icon {
  color: #9ca3af;
}

.sidecar-milestone-label {
  flex: 1;
  font-size: 13px;
  color: #374151;
}

.sidecar-milestone-arrow {
  font-size: 10px;
  color: rgba(91, 102, 122, 0.7);
}

.sidecar-milestone-details {
  padding: 8px 12px 12px 40px;
  border-top: 1px solid #e5e7eb;
}

/* Substeps */
.sidecar-substeps {
  display: flex;
  flex-direction: column;
  gap: 4px;
  margin-bottom: 8px;
}

.sidecar-substep {
  display: flex;
  align-items: center;
  gap: 6px;
  font-size: 12px;
  color: #6b7280;
}

.sidecar-substep-icon {
  font-size: 8px;
}

.sidecar-substep--complete .sidecar-substep-icon {
  color: #22c55e;
}

.sidecar-substep--current .sidecar-substep-icon {
  color: #2563eb;
}

/* Timeline */
.sidecar-timeline {
  border-left: 2px solid #e5e7eb;
  padding-left: 12px;
}

.sidecar-timeline-title {
  font-size: 11px;
  font-weight: 600;
  color: #9ca3af;
  margin-bottom: 8px;
}

.sidecar-timeline-entry {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  gap: 6px;
  font-size: 12px;
  margin-bottom: 6px;
  position: relative;
}

.sidecar-timeline-entry::before {
  content: '';
  position: absolute;
  left: -16px;
  width: 8px;
  height: 8px;
  background: #e5e7eb;
  border-radius: 50%;
}

.sidecar-timeline-actor {
  font-weight: 500;
  color: #374151;
}

.sidecar-timeline-actor--mobius {
  color: #2563eb;
}

.sidecar-timeline-actor--user {
  color: #059669;
}

.sidecar-timeline-action {
  color: #6b7280;
}

.sidecar-timeline-time {
  color: #9ca3af;
  font-size: 11px;
}

.sidecar-timeline-artifact {
  color: #2563eb;
  text-decoration: none;
  font-size: 11px;
}

.sidecar-timeline-artifact:hover {
  text-decoration: underline;
}

/* Payer Info */
.sidecar-payer-section {
  margin-top: 16px;
  padding-top: 16px;
  border-top: 1px solid #e5e7eb;
}

.sidecar-payer-info {
  background: #f9fafb;
  border-radius: 6px;
  padding: 12px;
}

.sidecar-payer-name {
  font-size: 14px;
  font-weight: 600;
  color: #1f2937;
  margin-bottom: 8px;
}

.sidecar-payer-phone,
.sidecar-payer-portal,
.sidecar-payer-response-time {
  font-size: 12px;
  color: #6b7280;
  margin-bottom: 4px;
}

.sidecar-payer-phone a,
.sidecar-payer-portal a {
  color: #2563eb;
  text-decoration: none;
}

.sidecar-payer-phone a:hover,
.sidecar-payer-portal a:hover {
  text-decoration: underline;
}

.sidecar-policy-excerpts {
  margin-top: 12px;
}

.sidecar-excerpts-title {
  font-size: 11px;
  font-weight: 600;
  color: #9ca3af;
  margin-bottom: 8px;
}

.sidecar-policy-excerpt {
  background: white;
  border: 1px solid #e5e7eb;
  border-radius: 6px;
  padding: 10px;
  margin-bottom: 8px;
}

.sidecar-excerpt-topic {
  font-size: 12px;
  font-weight: 600;
  color: #374151;
  margin-bottom: 4px;
}

.sidecar-excerpt-content {
  font-size: 12px;
  color: #6b7280;
  line-height: 1.4;
}

.sidecar-excerpt-source {
  font-size: 10px;
  color: #9ca3af;
  margin-top: 4px;
}

/* Quick Chat */
/* Quick Chat - matches Mini note input styling */
.sidecar-quick-chat {
  padding: 10px 10px;
  border-top: 1px solid rgba(11, 18, 32, 0.08);
  background: #ffffff;
}

.sidecar-quick-chat-wrapper {
  display: flex;
  align-items: center;
  position: relative;
}

.sidecar-quick-chat-input {
  flex: 1;
  height: 28px;
  padding: 0 32px 0 8px;
  font-size: 9px;
  font-weight: 400;
  border: 1px solid rgba(11, 18, 32, 0.12);
  border-radius: 6px;
  outline: none;
  color: #0b1220;
  background: #fff;
}

.sidecar-quick-chat-input::placeholder {
  color: rgba(91, 102, 122, 0.5);
  font-weight: 400;
}

.sidecar-quick-chat-input:focus {
  border-color: rgba(37, 99, 235, 0.35);
}

.sidecar-quick-chat-input:disabled {
  background: rgba(11, 18, 32, 0.02);
}

.sidecar-quick-chat-send {
  position: absolute;
  right: 0;
  width: 28px;
  height: 28px;
  background: transparent;
  border: none;
  border-radius: 6px;
  font-size: 12px;
  cursor: pointer;
  transition: background 0.15s ease;
  display: grid;
  place-items: center;
}

.sidecar-quick-chat-send:hover:not(:disabled) {
  background: rgba(11, 18, 32, 0.04);
}

.sidecar-quick-chat-send:disabled {
  cursor: not-allowed;
}

.sidecar-quick-chat-send svg {
  width: 14px;
  height: 14px;
  display: block;
  fill: rgba(91, 102, 122, 0.6);
}

.sidecar-quick-chat-send:hover:not(:disabled) svg {
  fill: rgba(91, 102, 122, 0.9);
}

.sidecar-quick-chat-send:disabled svg {
  fill: rgba(11, 18, 32, 0.2);
}

.sidecar-quick-chat-hint {
  font-size: 9px;
  font-weight: 400;
  color: rgba(91, 102, 122, 0.5);
  margin-top: 6px;
}

.sidecar-quick-chat-response {
  background: #f0f9ff;
  border: 1px solid #bae6fd;
  border-radius: 8px;
  padding: 12px;
  margin-bottom: 8px;
  position: relative;
}

.sidecar-quick-chat-response-text {
  font-size: 13px;
  color: #1e3a5f;
  line-height: 1.4;
}

.sidecar-quick-chat-response-source {
  font-size: 11px;
  color: #6b7280;
  margin-top: 6px;
}

.sidecar-quick-chat-response-dismiss {
  position: absolute;
  top: 8px;
  right: 8px;
  background: none;
  border: none;
  color: #9ca3af;
  cursor: pointer;
  font-size: 16px;
}

/* Sidecar Menu */
/* Sidecar Menu - matches Mini menu styling */
.sidecar-menu {
  position: relative;
}

.sidecar-menu-btn {
  width: 22px;
  height: 22px;
  border-radius: 6px;
  border: 1px solid rgba(11, 18, 32, 0.12);
  background: rgba(11, 18, 32, 0.03);
  cursor: pointer;
  display: grid;
  place-items: center;
  padding: 0;
}

.sidecar-menu-btn svg {
  width: 14px;
  height: 14px;
  fill: rgba(11, 18, 32, 0.7);
}

.sidecar-menu-btn:hover {
  background: rgba(11, 18, 32, 0.06);
}

.sidecar-menu-dropdown {
  position: absolute;
  top: 100%;
  right: 0;
  margin-top: 4px;
  min-width: 140px;
  background: #fff;
  border: 1px solid rgba(11, 18, 32, 0.12);
  border-radius: 10px;
  box-shadow: 0 8px 24px rgba(2, 6, 23, 0.12);
  z-index: 2147483647;
  overflow: visible;
  pointer-events: auto;
  padding: 4px 0;
}

.sidecar-menu-item {
  display: flex;
  align-items: center;
  gap: 8px;
  width: 100%;
  text-align: left;
  padding: 8px 12px;
  border: none;
  background: transparent;
  font-size: 11px;
  font-weight: 400;
  color: #0b1220;
  cursor: pointer;
  pointer-events: auto;
  transition: background 0.15s ease;
  position: relative;
}

.sidecar-menu-item:hover {
  background: rgba(11, 18, 32, 0.04);
}

.sidecar-menu-item.danger {
  color: #b3261e;
}

.sidecar-menu-icon {
  width: 14px;
  height: 14px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-shrink: 0;
}

.sidecar-menu-icon svg {
  width: 14px;
  height: 14px;
  fill: rgba(11, 18, 32, 0.6);
}

.sidecar-menu-label {
  flex: 1;
  font-size: 11px;
  font-weight: 400;
}

.sidecar-menu-arrow {
  font-size: 10px;
  color: rgba(91, 102, 122, 0.6);
  margin-left: auto;
}

.sidecar-menu-divider {
  height: 1px;
  background: rgba(11, 18, 32, 0.08);
  margin: 4px 0;
}

/* Theme submenu - positioned to the left since sidecar is on right edge */
.sidecar-menu-submenu {
  position: absolute;
  right: 100%;
  left: auto;
  top: 0;
  margin-right: 4px;
  min-width: 100px;
  background: #fff;
  border: 1px solid rgba(11, 18, 32, 0.12);
  border-radius: 8px;
  box-shadow: 0 4px 12px rgba(2, 6, 23, 0.1);
  padding: 4px 0;
  z-index: 2147483648;
}

.sidecar-menu-submenu-item {
  display: flex;
  align-items: center;
  gap: 6px;
  padding: 6px 10px;
  font-size: 10px;
  font-weight: 400;
  color: #0b1220;
  cursor: pointer;
  transition: background 0.15s ease;
}

.sidecar-menu-submenu-item:hover {
  background: rgba(11, 18, 32, 0.04);
}

.sidecar-menu-theme-icon {
  font-size: 8px;
  color: rgba(91, 102, 122, 0.7);
}

.sidecar-menu-theme-label {
  font-size: 10px;
  font-weight: 400;
}

/* Toggle switch - matches Mini style */
.sidecar-menu-toggle {
  position: relative;
  width: 32px;
  height: 18px;
}

.sidecar-menu-toggle-input {
  opacity: 0;
  width: 0;
  height: 0;
}

.sidecar-menu-toggle-slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(11, 18, 32, 0.15);
  border-radius: 9px;
  transition: background 0.2s ease;
}

.sidecar-menu-toggle-slider::before {
  content: '';
  position: absolute;
  height: 14px;
  width: 14px;
  left: 2px;
  bottom: 2px;
  background: white;
  border-radius: 50%;
  transition: transform 0.2s ease;
  box-shadow: 0 1px 3px rgba(2, 6, 23, 0.15);
}

.sidecar-menu-toggle-input:checked + .sidecar-menu-toggle-slider {
  background: #2563eb;
}

.sidecar-menu-toggle-input:checked + .sidecar-menu-toggle-slider::before {
  transform: translateX(14px);
}

/* Collapse button - matches Mini expand/minimize buttons */
.sidecar-collapse-btn {
  width: 22px;
  height: 22px;
  border-radius: 6px;
  border: 1px solid rgba(11, 18, 32, 0.12);
  background: rgba(11, 18, 32, 0.03);
  cursor: pointer;
  display: grid;
  place-items: center;
  padding: 0;
}

.sidecar-collapse-btn svg {
  width: 14px;
  height: 14px;
  fill: rgba(11, 18, 32, 0.7);
}

.sidecar-collapse-btn:hover {
  background: rgba(11, 18, 32, 0.06);
}

/* Alert Indicator */
/* Alert Indicator - matches Mini button styling */
.sidecar-alert-indicator {
  position: relative;
  width: 22px;
  height: 22px;
  border-radius: 6px;
  border: 1px solid rgba(11, 18, 32, 0.12);
  background: rgba(11, 18, 32, 0.03);
  cursor: pointer;
  display: grid;
  place-items: center;
}

.sidecar-alert-indicator:hover {
  background: rgba(11, 18, 32, 0.06);
}

.sidecar-alert-icon {
  width: 14px;
  height: 14px;
  display: inline-flex;
  align-items: center;
  justify-content: center;
}

.sidecar-alert-icon svg {
  width: 14px;
  height: 14px;
  display: block;
  fill: none;
  stroke: rgba(11, 18, 32, 0.85);
  stroke-width: 1.6;
  stroke-linecap: round;
  stroke-linejoin: round;
}

.sidecar-alert-badge {
  position: absolute;
  top: -4px;
  right: -4px;
  min-width: 14px;
  height: 14px;
  background: #2563eb;
  color: white;
  font-size: 9px;
  font-weight: 600;
  border-radius: 7px;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0 3px;
}

.sidecar-alert-badge--high {
  background: #dc2626;
}

.sidecar-notification-dot {
  width: 6px;
  height: 6px;
  background: #2563eb;
  border-radius: 50%;
}

.sidecar-notification-dot--high {
  background: #dc2626;
}

/* Privacy indicator - matches Mini style */
.sidecar-privacy-indicator {
  font-size: 11px;
  color: rgba(91, 102, 122, 0.95);
}

