hB;;AAEA;EACE,cAAc;AAChB;;AAEA;EACE,wCAAwC;AAC1C;;AAEA,eAAe;AACf;;;EAGE,mCAAmC;EACnC,oCAAoC;AACtC;;AAEA;;;EAGE,aAAa;AACf;;AAEA;;EAEE,mBAAmB;EACnB,qCAAqC;AACvC;;AAEA;EACE,cAAc;AAChB;;AAEA;EACE,mCAAmC;AACrC;;AAEA;EACE,aAAa;AACf;;AAEA,qBAAqB;AACrB;EACE,oCAAoC;EACpC,wCAAwC;AAC1C;;AAEA;EACE,mBAAmB;EACnB,qBAAqB;AACvB;;AAEA;EACE,qBAAqB;AACvB;;AAEA;EACE,aAAa;AACf","sourcesContent":["/**\n * Sidebar-only styles for Mobius OS (content script injection)\n * IMPORTANT: Must not style host page elements like html/body.\n * Everything is scoped under #mobius-os-sidebar to avoid collisions.\n */\n\n#mobius-os-sidebar,\n#mobius-os-sidebar * {\n  box-sizing: border-box;\n}\n\n#mobius-os-sidebar {\n  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, 'Google Sans', sans-serif;\n  background: #ffffff;\n  color: #0b1220;\n  font-size: 12px;\n  font-weight: 500;\n}\n\n/* Subtle, modern look - matching Mini */\n#mobius-os-sidebar {\n  border-left: 1px solid rgba(11, 18, 32, 0.12);\n  box-shadow: -4px 0 24px rgba(2, 6, 23, 0.08);\n}\n\n#mobius-os-sidebar button,\n#mobius-os-sidebar input,\n#mobius-os-sidebar select {\n  font: inherit;\n}\n\n#mobius-os-sidebar button {\n  cursor: pointer;\n}\n\n#mobius-os-sidebar .top-row,\n#mobius-os-sidebar .second-row,\n#mobius-os-sidebar .tasks-panel,\n#mobius-os-sidebar .chat-input-area,\n#mobius-os-sidebar .record-id-input,\n#mobius-os-sidebar .workflow-buttons,\n#mobius-os-sidebar .footer {\n  padding-left: 14px;\n  padding-right: 14px;\n}\n\n/* Top Row / Header */\n#mobius-os-sidebar .top-row {\n  display: flex;\n  align-items: center;\n  justify-content: flex-start;\n  gap: 8px;\n  padding-top: 8px;\n  padding-bottom: 10px;\n  border-bottom: 1px solid rgba(60, 64, 67, 0.12);\n  background: white;\n  flex-shrink: 0;\n}\n\n#mobius-os-sidebar .header-actions {\n  display: flex;\n  align-items: center;\n  gap: 8px;\n  margin-left: auto;\n  flex-shrink: 0;\n}\n\n#mobius-os-sidebar .header-left {\n  display: flex;\n  align-items: center;\n  gap: 12px;\n  flex: 0 1 auto;\n  min-width: 0;\n}\n\n#mobius-os-sidebar .client-logo {\n  display: flex;\n  align-items: center;\n  gap: 6px;\n  font-size: 11px;\n  color: #5f6368;\n  min-width: 100px;\n}\n\n#mobius-os-sidebar .client-logo img {\n  width: 24px;\n  height: 24px;\n  border-radius: 4px;\n}\n\n#mobius-os-sidebar .client-logo-svg {\n  width: 24px;\n  height: 24px;\n  flex-shrink: 0;\n}\n\n#mobius-os-sidebar .client-logo-svg svg {\n  width: 100%;\n  height: 100%;\n  display: block;\n}\n\n#mobius-os-sidebar .client-logo-placeholder {\n  width: 24px;\n  height: 24px;\n  background: #e8eaed;\n  border-radius: 4px;\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  font-size: 10px;\n  color: #5f6368;\n}\n\n#mobius-os-sidebar .client-name {\n  font-weight: 500;\n  color: #202124;\n}\n\n#mobius-os-sidebar .logo-section {\n  display: flex;\n  align-items: center;\n  gap: 8px;\n  flex-wrap: nowrap;\n  min-width: 0;\n}\n\n#mobius-os-sidebar .mobius-logo {\n  width: 32px;\n  height: 32px;\n  position: relative;\n}\n\n#mobius-os-sidebar .mobius-logo svg {\n  width: 100%;\n  height: 100%;\n}\n\n#mobius-os-sidebar .mobius-logo .path-normal {\n  display: none;\n}\n\n#mobius-os-sidebar .mobius-logo .path-processing {\n  display: block;\n}\n\n#mobius-os-sidebar .mobius-logo:not(.status-processing) .path-normal {\n  display: block;\n}\n\n#mobius-os-sidebar .mobius-logo:not(.status-processing) .path-processing {\n  display: none;\n}\n\n#mobius-os-sidebar .logo-label {\n  font-weight: 500;\n  font-size: 12px;\n  color: #202124;\n  white-space: nowrap;\n  overflow: hidden;\n  text-overflow: ellipsis;\n  max-width: 120px;\n}\n\n#mobius-os-sidebar .context-display {\n  flex: 1 1 auto;\n  margin: 0;\n  font-size: 11px;\n  color: #5f6368;\n  display: flex;\n  align-items: center;\n  gap: 6px;\n  justify-content: center;\n  white-space: nowrap;\n  overflow: hidden;\n  text-overflow: ellipsis;\n  min-width: 0;\n}\n\n#mobius-os-sidebar .status-indicator {\n  width: 8px;\n  height: 8px;\n  border-radius: 50%;\n  flex-shrink: 0;\n}\n\n#mobius-os-sidebar .status-proceed,\n#mobius-os-sidebar .status-green {\n  background: #16a34a;\n  box-shadow: 0 0 4px rgba(22, 163, 74, 0.6);\n}\n\n#mobius-os-sidebar .status-pending,\n#mobius-os-sidebar .status-yellow {\n  background: #f59e0b;\n  box-shadow: 0 0 4px rgba(245, 158, 11, 0.6);\n}\n\n#mobius-os-sidebar .status-error,\n#mobius-os-sidebar .status-red {\n  background: #dc2626;\n  box-shadow: 0 0 4px rgba(220, 38, 38, 0.6);\n}\n\n#mobius-os-sidebar .status-blue {\n  background: #2563eb;\n  box-shadow: 0 0 4px rgba(37, 99, 235, 0.6);\n}\n\n#mobius-os-sidebar .status-grey {\n  background: #94a3b8;\n  box-shadow: 0 0 4px rgba(148, 163, 184, 0.6);\n}\n\n#mobius-os-sidebar .mode-badge {\n  display: inline-block;\n  padding: 3px 8px;\n  background: rgba(26, 115, 232, 0.10);\n  color: #1a73e8;\n  border-radius: 12px;\n  font-size: 10px;\n  font-weight: 600;\n  margin-left: 8px;\n  text-transform: uppercase;\n}\n\n#mobius-os-sidebar .alert-btn,\n#mobius-os-sidebar .settings-btn {\n  width: 32px;\n  height: 32px;\n  border: 1px solid rgba(60, 64, 67, 0.18);\n  background: white;\n  border-radius: 6px;\n  cursor: pointer;\n  display: flex;\n  align-items: center;\n  justify-content: center;\n}\n\n#mobius-os-sidebar .alert-btn {\n  font-size: 16px;\n  position: relative;\n  margin-right: 8px;\n}\n\n#mobius-os-sidebar .settings-btn {\n  font-size: 18px;\n}\n\n#mobius-os-sidebar .alert-btn:hover,\n#mobius-os-sidebar .settings-btn:hover {\n  background: #f5f5f5;\n}\n\n/* Collapse button (added to header actions) */\n#mobius-os-sidebar .collapse-btn {\n  width: 32px;\n  height: 32px;\n  border: 1px solid rgba(60, 64, 67, 0.18);\n  background: white;\n  border-radius: 6px;\n  cursor: pointer;\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  font-size: 16px;\n  line-height: 1;\n}\n\n#mobius-os-sidebar .collapse-btn:hover {\n  background: #f5f5f5;\n}\n\n/* Second Row */\n#mobius-os-sidebar .second-row {\n  padding-top: 10px;\n  padding-bottom: 10px;\n  border-bottom: 1px solid rgba(60, 64, 67, 0.12);\n  background: white;\n  flex-shrink: 0;\n}\n\n#mobius-os-sidebar .context-summary {\n  font-size: 12px;\n  color: #202124;\n  margin-bottom: 6px;\n  line-height: 1.4;\n}\n\n#mobius-os-sidebar .quick-action-btn {\n  display: inline-block;\n  padding: 7px 12px;\n  background: #1a73e8;\n  color: white;\n  border-radius: 10px;\n  font-size: 12px;\n  cursor: pointer;\n  border: none;\n}\n\n#mobius-os-sidebar .quick-action-btn:hover {\n  background: #1565c0;\n}\n\n/* Tasks Panel */\n#mobius-os-sidebar .tasks-panel {\n  padding-top: 8px;\n  padding-bottom: 8px;\n  border-bottom: 1px solid rgba(60, 64, 67, 0.12);\n  background: #fff;\n  max-height: 150px;\n  overflow-y: auto;\n  flex-shrink: 0;\n}\n\n#mobius-os-sidebar .tasks-panel-header {\n  display: flex;\n  justify-content: space-between;\n  align-items: center;\n  cursor: pointer;\n  font-size: 11px;\n  font-weight: 600;\n  color: #5f6368;\n  margin-bottom: 6px;\n}\n\n#mobius-os-sidebar .tasks-panel-content {\n  display: none;\n}\n\n#mobius-os-sidebar .tasks-panel-content.show {\n  display: block;\n}\n\n#mobius-os-sidebar .task-item {\n  display: flex;\n  align-items: flex-start;\n  gap: 8px;\n  padding: 4px 0;\n  font-size: 12px;\n  color: #202124;\n}\n\n#mobius-os-sidebar .task-item.shared {\n  color: #1a73e8;\n}\n\n#mobius-os-sidebar .task-item.backend {\n  color: #5f6368;\n  font-style: italic;\n}\n\n/* Chat Area */\n#mobius-os-sidebar .chat-area {\n  flex: 1;\n  overflow-y: auto;\n  padding: 10px 12px;\n  background: #ffffff;\n  display: flex;\n  flex-direction: column;\n  gap: 10px;\n  justify-content: flex-end;\n  min-height: 0;\n  flex-shrink: 1;\n}\n\n#mobius-os-sidebar .message {\n  max-width: 75%;\n  padding: 7px 10px;\n  border-radius: 10px;\n  font-size: 11px;\n  line-height: 1.45;\n  color: #0b1220;\n}\n\n#mobius-os-sidebar .system-message {\n  /* Stretch to full width to avoid shrink-to-fit issues with inline-block bubble max-width. */\n  align-self: stretch;\n  width: 100%;\n  background: transparent;\n  border: none;\n  padding: 0;\n  max-width: 100%;\n  color: #0b1220;\n}\n\n#mobius-os-sidebar .message-content {\n  white-space: pre-wrap;\n  word-break: break-word;\n}\n\n/* Compact assistant \"bubble\" */\n#mobius-os-sidebar .system-message .message-content {\n  margin-top: 4px;\n  display: inline-block;\n  background: rgba(11, 18, 32, 0.03);\n  border: 1px solid rgba(11, 18, 32, 0.10);\n  padding: 7px 10px;\n  border-radius: 10px;\n  max-width: 92%;\n}\n\n/* Make tools unobtrusive (Mini-style) */\n#mobius-os-sidebar .system-message .message-header {\n  margin-bottom: 2px;\n}\n\n#mobius-os-sidebar .system-message .message-tools-btn {\n  width: 22px;\n  height: 22px;\n  border: 1px solid rgba(11, 18, 32, 0.12);\n  background: rgba(11, 18, 32, 0.03);\n  border-radius: 6px;\n  opacity: 0.0;\n  transition: opacity 0.12s ease, background 0.15s ease;\n}\n\n#mobius-os-sidebar .system-message:hover .message-tools-btn {\n  opacity: 0.85;\n}\n\n#mobius-os-sidebar .system-message .message-tools-btn:hover {\n  opacity: 1;\n  background: rgba(11, 18, 32, 0.06);\n}\n\n#mobius-os-sidebar .user-message {\n  align-self: flex-end;\n  background: #2563eb;\n  color: white;\n}\n\n/* Thinking (minimal, collapsible line) */\n#mobius-os-sidebar .thinking-line {\n  margin: 0 0 6px 0;\n  padding: 0;\n  border: none;\n  background: transparent;\n  color: rgba(91, 102, 122, 0.7);\n  font-size: 11px;\n}\n\n#mobius-os-sidebar .thinking-line summary {\n  list-style: none;\n  cursor: pointer;\n  user-select: none;\n  display: flex;\n  align-items: center;\n  gap: 6px;\n  padding: 0;\n}\n\n#mobius-os-sidebar .thinking-line summary::-webkit-details-marker {\n  display: none;\n}\n\n#mobius-os-sidebar .thinking-line summary::after {\n  content: '';\n  margin-left: auto;\n  opacity: 0.7;\n}\n\n#mobius-os-sidebar .thinking-line[open] summary::after {\n  content: '';\n}\n\n#mobius-os-sidebar .thinking-body {\n  margin-top: 4px;\n  padding-left: 10px;\n  border-left: 2px solid rgba(11, 18, 32, 0.12);\n  white-space: pre-wrap;\n  word-break: break-word;\n}\n\n/* Chat Input */\n#mobius-os-sidebar .chat-input-area {\n  padding-top: 8px;\n  padding-bottom: 8px;\n  border-top: 1px solid rgba(11, 18, 32, 0.08);\n  background: white;\n  display: flex;\n  align-items: center;\n  gap: 8px;\n  flex-shrink: 0;\n  width: 100%;\n}\n\n#mobius-os-sidebar .chat-input {\n  flex: 1;\n  width: 100%;\n  min-width: 0;\n  padding: 8px 12px;\n  border: 1px solid rgba(11, 18, 32, 0.12);\n  border-radius: 10px;\n  font-size: 11px;\n  outline: none;\n  background: #fff;\n  resize: none;\n  overflow: hidden;\n  line-height: 1.4;\n  -webkit-appearance: none;\n  appearance: none;\n  color: #0b1220;\n}\n\n#mobius-os-sidebar .chat-input::placeholder {\n  color: rgba(91, 102, 122, 0.7);\n}\n\n#mobius-os-sidebar .chat-input:focus {\n  border-color: #2563eb;\n}\n\n/* Message-level tools (system messages) */\n#mobius-os-sidebar .message-header {\n  display: flex;\n  justify-content: flex-end;\n  margin-bottom: 6px;\n}\n\n#mobius-os-sidebar .message-tools {\n  position: relative;\n}\n\n#mobius-os-sidebar .message-tools-btn {\n  width: 22px;\n  height: 22px;\n  border: 1px solid rgba(11, 18, 32, 0.12);\n  background: rgba(11, 18, 32, 0.03);\n  border-radius: 6px;\n  display: inline-flex;\n  align-items: center;\n  justify-content: center;\n  padding: 0;\n  line-height: 1;\n}\n\n#mobius-os-sidebar .message-tools-btn:hover {\n  background: rgba(11, 18, 32, 0.06);\n}\n\n#mobius-os-sidebar .message-tools-dropdown {\n  position: absolute;\n  top: 100%;\n  right: 0;\n  margin-top: 6px;\n  background: #fff;\n  border: 1px solid rgba(11, 18, 32, 0.12);\n  border-radius: 12px;\n  box-shadow: 0 14px 28px rgba(2, 6, 23, 0.18);\n  display: none;\n  flex-direction: column;\n  min-width: 140px;\n  z-index: 2;\n}\n\n#mobius-os-sidebar .message-tools-dropdown.show {\n  display: flex;\n}\n\n#mobius-os-sidebar .message-tools-item {\n  padding: 10px 12px;\n  border: none;\n  background: white;\n  text-align: left;\n  font-size: 12px;\n  font-weight: 600;\n  color: #0b1220;\n}\n\n#mobius-os-sidebar .message-tools-item:hover {\n  background: rgba(11, 18, 32, 0.04);\n}\n\n/* Chat tools (make compact, not stacked) */\n#mobius-os-sidebar .chat-tools {\n  display: flex;\n  align-items: center;\n  gap: 6px;\n}\n\n#mobius-os-sidebar .chat-tool-btn {\n  width: 22px;\n  height: 22px;\n  border: 1px solid rgba(11, 18, 32, 0.12);\n  background: rgba(11, 18, 32, 0.03);\n  border-radius: 6px;\n  display: inline-flex;\n  align-items: center;\n  justify-content: center;\n  padding: 0;\n}\n\n#mobius-os-sidebar .chat-tool-btn:hover {\n  background: rgba(60, 64, 67, 0.06);\n}\n\n#mobius-os-sidebar .tools-menu {\n  position: relative;\n}\n\n#mobius-os-sidebar .tools-dropdown {\n  position: absolute;\n  bottom: 100%;\n  right: 0;\n  margin-bottom: 6px;\n  background: white;\n  border: 1px solid rgba(60, 64, 67, 0.18);\n  border-radius: 10px;\n  box-shadow: 0 10px 24px rgba(0,0,0,0.10);\n  display: none;\n  flex-direction: column;\n  min-width: 140px;\n  z-index: 2;\n}\n\n#mobius-os-sidebar .tools-dropdown.show {\n  display: flex;\n}\n\n#mobius-os-sidebar .tools-dropdown button {\n  padding: 10px 12px;\n  border: none;\n  background: white;\n  text-align: left;\n  font-size: 12px;\n}\n\n#mobius-os-sidebar .tools-dropdown button:hover {\n  background: rgba(60, 64, 67, 0.06);\n}\n\n/* Record ID Input */\n#mobius-os-sidebar .record-id-input {\n  padding-top: 8px;\n  padding-bottom: 8px;\n  border-top: 1px solid rgba(60, 64, 67, 0.12);\n  background: white;\n  display: flex;\n  align-items: center;\n  gap: 8px;\n  flex-shrink: 0;\n}\n\n#mobius-os-sidebar .record-id-input label {\n  font-size: 11px;\n  color: #5f6368;\n  font-weight: 600;\n  white-space: nowrap;\n}\n\n#mobius-os-sidebar .record-id-input select {\n  padding: 6px 8px;\n  border: 1px solid rgba(60, 64, 67, 0.18);\n  border-radius: 8px;\n  font-size: 12px;\n  background: #fff;\n}\n\n#mobius-os-sidebar .record-id-input input {\n  flex: 1;\n  padding: 7px 10px;\n  border: 1px solid rgba(60, 64, 67, 0.18);\n  border-radius: 8px;\n  font-size: 12px;\n  outline: none;\n}\n\n/* Workflow Buttons */\n#mobius-os-sidebar .workflow-buttons {\n  padding-top: 8px;\n  padding-bottom: 8px;\n  border-top: 1px solid rgba(60, 64, 67, 0.12);\n  background: #fff;\n  display: flex;\n  gap: 8px;\n  flex-wrap: wrap;\n  flex-shrink: 0;\n}\n\n#mobius-os-sidebar .context-button {\n  padding: 8px 10px;\n  background: rgba(60, 64, 67, 0.06);\n  border: 1px solid rgba(60, 64, 67, 0.10);\n  border-radius: 10px;\n  font-size: 12px;\n}\n\n#mobius-os-sidebar .context-button:hover {\n  background: rgba(60, 64, 67, 0.10);\n}\n\n/* Footer */\n#mobius-os-sidebar .footer {\n  padding-top: 10px;\n  padding-bottom: 10px;\n  border-top: 1px solid rgba(60, 64, 67, 0.12);\n  background: #fff;\n  flex-shrink: 0;\n}\n\n#mobius-os-sidebar .user-details {\n  display: flex;\n  justify-content: space-between;\n  align-items: center;\n  gap: 10px;\n  flex-wrap: nowrap;\n}\n\n#mobius-os-sidebar .user-info {\n  font-size: 12px;\n  color: #5f6368;\n  white-space: nowrap;\n  overflow: hidden;\n  text-overflow: ellipsis;\n  min-width: 0;\n}\n\n#mobius-os-sidebar .preferences {\n  display: flex;\n  gap: 10px;\n  font-size: 11px;\n  align-items: center;\n  white-space: nowrap;\n}\n\n#mobius-os-sidebar .preferences-panel {\n  display: flex;\n  flex-direction: column;\n  align-items: flex-end;\n  min-width: 0;\n}\n\n#mobius-os-sidebar .preferences-expanded {\n  display: none;\n  margin-top: 6px;\n}\n\n#mobius-os-sidebar .preferences-expanded.show {\n  display: block;\n}\n\n#mobius-os-sidebar .preference-group {\n  display: flex;\n  align-items: center;\n  gap: 8px;\n  justify-content: flex-end;\n  margin-bottom: 6px;\n}\n\n#mobius-os-sidebar .preference-group label {\n  font-size: 11px;\n  color: #5f6368;\n  white-space: nowrap;\n}\n\n#mobius-os-sidebar .preference-group select {\n  padding: 6px 8px;\n  border: 1px solid rgba(60, 64, 67, 0.18);\n  border-radius: 8px;\n  font-size: 12px;\n  background: #fff;\n}\n\n#mobius-os-sidebar .preference-item {\n  display: inline-flex;\n  align-items: center;\n  gap: 6px;\n  padding: 3px 6px;\n  border-radius: 8px;\n}\n\n#mobius-os-sidebar .preference-item:hover {\n  background: rgba(60, 64, 67, 0.06);\n}\n\n#mobius-os-sidebar .pref-value {\n  color: #1a73e8;\n  font-weight: 600;\n  font-size: 11px;\n}\n\n/* Guidance / Follow-up actions (system message CTA buttons) */\n#mobius-os-sidebar .guidance-actions {\n  margin-top: 8px;\n  padding: 6px 8px;\n  display: flex;\n  gap: 6px;\n  flex-wrap: wrap;\n  align-items: center;\n  border-radius: 10px;\n  border: 1px solid rgba(26, 115, 232, 0.18);\n  background: rgba(26, 115, 232, 0.06);\n}\n\n#mobius-os-sidebar .guidance-action-btn {\n  appearance: none;\n  border: 1px solid rgba(26, 115, 232, 0.28);\n  background: rgba(26, 115, 232, 0.10);\n  color: #174ea6;\n  padding: 5px 10px;\n  border-radius: 999px;\n  font-size: 12px;\n  line-height: 1.2;\n}\n\n#mobius-os-sidebar .guidance-action-btn:hover {\n  background: rgba(26, 115, 232, 0.16);\n  border-color: rgba(26, 115, 232, 0.38);\n}\n\n/**\n * Mini widget styles (scoped; does not affect host page nor #mobius-os-sidebar).\n */\n#mobius-os-mini,\n#mobius-os-mini * {\n  box-sizing: border-box;\n}\n\n#mobius-os-mini {\n  position: fixed !important;\n  z-index: 2147483646 !important;\n  width: 300px;\n  background: #ffffff !important;\n  color: #0b1220 !important;\n  border: 1px solid rgba(11, 18, 32, 0.12) !important;\n  border-radius: 14px !important;\n  box-shadow: 0 16px 34px rgba(2, 6, 23, 0.14) !important;\n  overflow: hidden !important;\n  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, 'Google Sans', sans-serif !important;\n  font-size: 12px !important;\n  font-weight: 500 !important;\n  user-select: none !important;\n}\n\n#mobius-os-mini button,\n#mobius-os-mini input {\n  font: inherit;\n}\n\n#mobius-os-mini .mobius-mini-header {\n  display: flex;\n  align-items: center;\n  gap: 6px;\n  padding: 5px 8px;\n  border-bottom: 1px solid rgba(11, 18, 32, 0.08);\n  background: rgba(11, 18, 32, 0.02);\n  cursor: grab;\n  touch-action: none;\n}\n\n#mobius-os-mini .mobius-mini-drag-handle {\n  width: 20px;\n  height: 20px;\n  border-radius: 6px;\n  border: 1px solid rgba(11, 18, 32, 0.10);\n  background: rgba(11, 18, 32, 0.03);\n  display: grid;\n  place-items: center;\n  cursor: grab;\n  flex: 0 0 auto;\n  touch-action: none;\n}\n\n#mobius-os-mini .mobius-mini-drag-handle:active {\n  cursor: grabbing;\n}\n\n#mobius-os-mini .mobius-mini-drag-dots {\n  width: 8px;\n  height: 14px;\n  background:\n    radial-gradient(circle, rgba(11,18,32,0.35) 1.5px, transparent 2px) 0 0/5px 5px;\n  border-radius: 4px;\n}\n\n#mobius-os-mini .mobius-mini-brand {\n  display: inline-flex;\n  align-items: center;\n  gap: 5px;\n  min-width: 0;\n  flex: 1 1 auto;\n}\n\n#mobius-os-mini .mobius-mini-brand .mobius-logo {\n  width: 18px;\n  height: 18px;\n}\n\n/* Client logo in Mini widget */\n#mobius-os-mini .client-logo {\n  display: flex;\n  align-items: center;\n  gap: 4px;\n  flex-shrink: 0;\n}\n\n#mobius-os-mini .client-logo.client-logo-compact {\n  gap: 0;\n}\n\n#mobius-os-mini .client-logo img {\n  width: 18px;\n  height: 18px;\n  border-radius: 3px;\n}\n\n#mobius-os-mini .client-logo-svg {\n  width: 18px;\n  height: 18px;\n  flex-shrink: 0;\n}\n\n#mobius-os-mini .client-logo-svg svg {\n  width: 100%;\n  height: 100%;\n  display: block;\n}\n\n#mobius-os-mini .client-logo-placeholder {\n  width: 18px;\n  height: 18px;\n  background: #e8eaed;\n  border-radius: 3px;\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  font-size: 8px;\n  color: #5f6368;\n  font-weight: 600;\n}\n\n#mobius-os-mini .client-name {\n  font-size: 9px;\n  font-weight: 500;\n  color: #5f6368;\n  white-space: nowrap;\n}\n\n/* Divid