rder-bottom-color: rgba(255, 255, 255, 0.08);\n}\n\n#mobius-os-mini.theme-dark .mobius-mini-greeting-text {\n  color: #93c5fd;\n}\n\n#mobius-os-mini.theme-dark .mobius-mini-greeting-chevron {\n  color: #60a5fa;\n}\n\n#mobius-os-mini.theme-dark .mobius-mini-greeting-dismiss {\n  color: rgba(148, 163, 184, 0.7);\n}\n\n#mobius-os-mini.theme-blue .mobius-mini-greeting-row {\n  background: linear-gradient(90deg, rgba(37, 99, 235, 0.1), rgba(96, 165, 250, 0.08));\n}\n\n#mobius-os-mini.theme-blue .mobius-mini-greeting-text {\n  color: #1d4ed8;\n}\n\n/* Attention dropdown - compact */\n#mobius-os-mini .mobius-mini-attention-dropdown {\n  min-width: 180px !important;\n  max-width: 220px;\n}\n\n#mobius-os-mini .mobius-mini-note-wrap {\n  width: 100%;\n  position: relative;\n  display: flex;\n  align-items: center;\n}\n\n#mobius-os-mini .mobius-mini-note-input {\n  width: 100%;\n  height: 28px;\n  border-radius: 10px;\n  border: 1px solid rgba(11, 18, 32, 0.14);\n  background: #fff;\n  padding: 0 34px 0 8px;\n  font-size: 10px;\n  font-weight: 500;\n  color: #0b1220;\n  outline: none;\n}\n\n#mobius-os-mini .mobius-mini-note-input::placeholder {\n  color: rgba(91,102,122,0.75);\n  font-weight: 500;\n}\n\n#mobius-os-mini .mobius-mini-note-input:focus {\n  border-color: rgba(37, 99, 235, 0.55);\n}\n\n#mobius-os-mini .mobius-mini-send-btn {\n  position: absolute;\n  right: 6px;\n  width: 28px;\n  height: 28px;\n  border-radius: 10px;\n  border: 1px solid rgba(11, 18, 32, 0.12);\n  background: rgba(11, 18, 32, 0.03);\n  cursor: pointer;\n  display: grid;\n  place-items: center;\n  padding: 0;\n}\n\n#mobius-os-mini .mobius-mini-send-btn:hover {\n  background: rgba(11, 18, 32, 0.06);\n}\n\n#mobius-os-mini .mobius-mini-send-btn svg {\n  width: 16px;\n  height: 16px;\n  display: block;\n  fill: rgba(11, 18, 32, 0.85);\n}\n\n/* =============================================================================\n   Mini Widget Themes\n   ============================================================================= */\n\n/* Theme: Light (default) - already applied via base styles */\n#mobius-os-mini.theme-light {\n  background: #ffffff !important;\n  color: #0b1220 !important;\n  border-color: rgba(11, 18, 32, 0.12) !important;\n  box-shadow: 0 16px 34px rgba(2, 6, 23, 0.14) !important;\n}\n\n/* Theme: Dark */\n#mobius-os-mini.theme-dark {\n  background: #1e293b !important;\n  color: #f1f5f9 !important;\n  border-color: #334155 !important;\n  box-shadow: 0 16px 34px rgba(0, 0, 0, 0.4) !important;\n}\n\n#mobius-os-mini.theme-dark .mobius-mini-header {\n  background: rgba(255, 255, 255, 0.03);\n  border-bottom-color: rgba(255, 255, 255, 0.08);\n}\n\n#mobius-os-mini.theme-dark .mobius-mini-brand-text {\n  color: #f1f5f9;\n}\n\n#mobius-os-mini.theme-dark .mobius-mini-row {\n  border-bottom-color: rgba(255, 255, 255, 0.08);\n}\n\n#mobius-os-mini.theme-dark .mobius-mini-row-label {\n  color: rgba(148, 163, 184, 0.9);\n}\n\n#mobius-os-mini.theme-dark .mobius-mini-patient-name {\n  color: #f1f5f9;\n}\n\n#mobius-os-mini.theme-dark .mobius-mini-patient-id,\n#mobius-os-mini.theme-dark .mobius-mini-patient-dob {\n  color: rgba(148, 163, 184, 0.9);\n}\n\n#mobius-os-mini.theme-dark .mobius-mini-minimized-patient {\n  color: #e2e8f0;\n}\n\n#mobius-os-mini.theme-dark .mobius-mini-problem-text {\n  color: #e2e8f0;\n}\n\n#mobius-os-mini.theme-dark .mobius-mini-status-badge {\n  background: rgba(255, 255, 255, 0.08);\n  border-color: rgba(255, 255, 255, 0.15);\n  color: #94a3b8;\n}\n\n#mobius-os-mini.theme-dark .mobius-mini-note-input {\n  background: #0f172a;\n  border-color: rgba(255, 255, 255, 0.12);\n  color: #f1f5f9;\n}\n\n#mobius-os-mini.theme-dark .mobius-mini-note-input::placeholder {\n  color: rgba(148, 163, 184, 0.6);\n}\n\n#mobius-os-mini.theme-dark .mobius-mini-menu-btn,\n#mobius-os-mini.theme-dark .mobius-mini-expand-btn,\n#mobius-os-mini.theme-dark .mobius-mini-minimize-btn,\n#mobius-os-mini.theme-dark .mobius-mini-drag-handle,\n#mobius-os-mini.theme-dark .mobius-mini-icon-btn,\n#mobius-os-mini.theme-dark .mobius-mini-send-btn {\n  background: rgba(255, 255, 255, 0.06);\n  border-color: rgba(255, 255, 255, 0.12);\n  color: #e5e7eb;\n}\n\n#mobius-os-mini.theme-dark .mobius-mini-task-badge {\n  background: rgba(37, 99, 235, 0.15);\n  border-color: rgba(37, 99, 235, 0.3);\n}\n\n/* Theme: Blue */\n#mobius-os-mini.theme-blue {\n  background: #eff6ff !important;\n  color: #1e3a5f !important;\n  border-color: #bfdbfe !important;\n  box-shadow: 0 16px 34px rgba(37, 99, 235, 0.15) !important;\n}\n\n#mobius-os-mini.theme-blue .mobius-mini-header {\n  background: rgba(37, 99, 235, 0.05);\n  border-bottom-color: rgba(37, 99, 235, 0.1);\n}\n\n#mobius-os-mini.theme-blue .mobius-mini-brand-text {\n  color: #1e40af;\n}\n\n#mobius-os-mini.theme-blue .mobius-mini-row {\n  border-bottom-color: rgba(37, 99, 235, 0.1);\n}\n\n#mobius-os-mini.theme-blue .mobius-mini-row-label {\n  color: #3b82f6;\n}\n\n#mobius-os-mini.theme-blue .mobius-mini-patient-name {\n  color: #1e3a5f;\n}\n\n#mobius-os-mini.theme-blue .mobius-mini-status-badge {\n  background: rgba(37, 99, 235, 0.1);\n  border-color: rgba(37, 99, 235, 0.25);\n}\n\n#mobius-os-mini.theme-blue .mobius-mini-note-input {\n  background: #ffffff;\n  border-color: #bfdbfe;\n}\n\n/* Theme: Glass */\n#mobius-os-mini.theme-glass {\n  background: rgba(255, 255, 255, 0.8) !important;\n  color: #0b1220 !important;\n  border-color: rgba(255, 255, 255, 0.5) !important;\n  box-shadow: 0 16px 34px rgba(2, 6, 23, 0.12), 0 0 0 1px rgba(255,255,255,0.2) inset !important;\n  backdrop-filter: blur(12px) !important;\n  -webkit-backdrop-filter: blur(12px) !important;\n}\n\n#mobius-os-mini.theme-glass .mobius-mini-header {\n  background: rgba(255, 255, 255, 0.3);\n  border-bottom-color: rgba(11, 18, 32, 0.06);\n}\n\n#mobius-os-mini.theme-glass .mobius-mini-row {\n  border-bottom-color: rgba(11, 18, 32, 0.06);\n}\n\n#mobius-os-mini.theme-glass .mobius-mini-note-input {\n  background: rgba(255, 255, 255, 0.6);\n}\n\n#mobius-os-mini.theme-glass .mobius-mini-menu-btn,\n#mobius-os-mini.theme-glass .mobius-mini-expand-btn,\n#mobius-os-mini.theme-glass .mobius-mini-minimize-btn,\n#mobius-os-mini.theme-glass .mobius-mini-drag-handle {\n  background: rgba(255, 255, 255, 0.4);\n}\n\n#mobius-os-mini.theme-glass .mobius-mini-minimized-patient {\n  color: #1e293b;\n}\n\n/* Patient edit modal */\n.mobius-mini-modal-overlay {\n  position: fixed;\n  inset: 0;\n  background: rgba(2, 6, 23, 0.28);\n  z-index: 2147483647;\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  padding: 18px;\n}\n\n.mobius-mini-modal {\n  width: 360px;\n  max-width: calc(100vw - 36px);\n  background: #fff;\n  border: 1px solid rgba(11, 18, 32, 0.12);\n  border-radius: 14px;\n  box-shadow: 0 18px 40px rgba(2, 6, 23, 0.20);\n  overflow: hidden;\n  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, 'Google Sans', sans-serif;\n  color: #0b1220;\n}\n\n.mobius-mini-modal-header {\n  padding: 10px 12px;\n  border-bottom: 1px solid rgba(11, 18, 32, 0.08);\n  display: flex;\n  align-items: center;\n  justify-content: space-between;\n  gap: 10px;\n}\n\n.mobius-mini-modal-title {\n  font-size: 12px;\n  font-weight: 600;\n}\n\n.mobius-mini-modal-close {\n  width: 28px;\n  height: 28px;\n  border-radius: 10px;\n  border: 1px solid rgba(11, 18, 32, 0.12);\n  background: rgba(11, 18, 32, 0.03);\n  cursor: pointer;\n  display: grid;\n  place-items: center;\n  font-weight: 600;\n}\n\n.mobius-mini-modal-close:hover {\n  background: rgba(11, 18, 32, 0.06);\n}\n\n.mobius-mini-modal-body {\n  padding: 14px 16px 12px;\n  display: flex;\n  flex-direction: column;\n  gap: 12px;\n}\n\n.mobius-mini-field {\n  display: flex;\n  flex-direction: column;\n  gap: 6px;\n}\n\n.mobius-mini-field-label {\n  font-size: 9px;\n  letter-spacing: 0.10em;\n  text-transform: uppercase;\n  color: rgba(91,102,122,0.95);\n  font-weight: 600;\n  margin-bottom: 0;\n}\n\n.mobius-mini-search-input {\n  position: relative;\n  display: flex;\n  align-items: center;\n}\n\n.mobius-mini-search-icon {\n  position: absolute;\n  left: 10px;\n  width: 16px;\n  height: 16px;\n  display: grid;\n  place-items: center;\n  opacity: 0.75;\n  pointer-events: none;\n}\n\n.mobius-mini-search-icon svg {\n  width: 16px;\n  height: 16px;\n  fill: rgba(91, 102, 122, 0.95);\n}\n\n.mobius-mini-input.mobius-mini-input-with-icon {\n  padding-left: 34px;\n}\n\n.mobius-mini-input {\n  width: 100%;\n  height: 34px;\n  border-radius: 12px;\n  border: 1px solid rgba(11, 18, 32, 0.14);\n  padding: 0 10px;\n  font-size: 12px;\n  font-weight: 500;\n  outline: none;\n}\n\n.mobius-mini-input:focus {\n  border-color: rgba(37, 99, 235, 0.55);\n}\n\n.mobius-mini-field-hint {\n  font-size: 12px;\n  color: rgba(91,102,122,0.95);\n}\n\n.mobius-mini-suggestions {\n  display: none;\n  border: 1px solid rgba(11, 18, 32, 0.12);\n  border-radius: 12px;\n  background: #fff;\n  overflow: hidden;\n  box-shadow: 0 12px 24px rgba(2,6,23,0.12);\n}\n\n.mobius-mini-suggestion {\n  width: 100%;\n  display: flex;\n  align-items: center;\n  justify-content: space-between;\n  gap: 10px;\n  padding: 10px 12px;\n  border: none;\n  background: #fff;\n  cursor: pointer;\n  font: inherit;\n}\n\n.mobius-mini-suggestion:hover {\n  background: rgba(11, 18, 32, 0.04);\n}\n\n.mobius-mini-suggestion.active {\n  background: rgba(37, 99, 235, 0.10);\n}\n\n.mobius-mini-suggestion .name {\n  font-size: 12px;\n  font-weight: 600;\n  color: #0b1220;\n  min-width: 0;\n  overflow: hidden;\n  text-overflow: ellipsis;\n  white-space: nowrap;\n}\n\n.mobius-mini-suggestion .id {\n  font-size: 11px;\n  font-weight: 500;\n  color: rgba(91,102,122,0.95);\n  white-space: nowrap;\n  flex: 0 0 auto;\n}\n\n.mobius-mini-modal-actions {\n  padding: 10px 12px;\n  border-top: 1px solid rgba(11, 18, 32, 0.08);\n  display: flex;\n  justify-content: flex-end;\n  gap: 8px;\n}\n\n.mobius-mini-btn-secondary,\n.mobius-mini-btn-primary {\n  height: 34px;\n  border-radius: 12px;\n  padding: 0 12px;\n  font-size: 12px;\n  font-weight: 600;\n  cursor: pointer;\n}\n\n.mobius-mini-btn-secondary {\n  border: 1px solid rgba(11, 18, 32, 0.12);\n  background: rgba(11, 18, 32, 0.03);\n}\n\n.mobius-mini-btn-secondary:hover {\n  background: rgba(11, 18, 32, 0.06);\n}\n\n.mobius-mini-btn-primary {\n  border: 1px solid rgba(37, 99, 235, 0.22);\n  background: rgba(37, 99, 235, 0.12);\n  color: #1e40af;\n}\n\n.mobius-mini-btn-primary:hover {\n  background: rgba(37, 99, 235, 0.16);\n}\n\n/* Mini toast */\n.mobius-mini-toast {\n  position: fixed;\n  left: 50%;\n  top: 16px;\n  transform: translateX(-50%);\n  background: rgba(15,23,42,0.92);\n  color: #fff;\n  padding: 10px 12px;\n  border-radius: 12px;\n  font-size: 12px;\n  font-weight: 600;\n  z-index: 2147483647;\n  box-shadow: 0 14px 28px rgba(2,6,23,0.22);\n}\n\n/* =============================================================================\n   SIDECAR COMPONENTS\n   Minimalist, bottleneck-focused design\n   ============================================================================= */\n\n/* Status Bar - Modern minimal design */\n.sidecar-status-bar {\n  padding: 6px 12px;\n  background: #ffffff;\n  border-bottom: 1px solid rgba(11, 18, 32, 0.08);\n  display: flex;\n  flex-direction: column;\n  gap: 4px;\n}\n\n.sidecar-status-bar-title-row {\n  display: flex;\n  align-items: center;\n  justify-content: space-between;\n  gap: 8px;\n}\n\n.sidecar-status-bar-title {\n  font-size: 7px;\n  font-weight: 600;\n  color: rgba(91, 102, 122, 0.95);\n  text-transform: uppercase;\n  letter-spacing: 0.04em;\n}\n\n.sidecar-status-bar-context {\n  font-size: 8px;\n  font-weight: 400;\n  color: rgba(91, 102, 122, 0.8);\n  white-space: nowrap;\n}\n\n.sidecar-status-bar-progress-row {\n  display: flex;\n  align-items: center;\n  gap: 6px;\n}\n\n.sidecar-status-bar-track-wrapper {\n  flex: 1;\n  min-width: 0;\n}\n\n.sidecar-status-bar-track {\n  position: relative;\n  height: 3px;\n  border-radius: 2px;\n  /* Mono grey gradient - subtle, modern */\n  background: linear-gradient(90deg, \n    rgba(11, 18, 32, 0.08) 0%, \n    rgba(11, 18, 32, 0.12) 50%,\n    rgba(11, 18, 32, 0.06) 100%\n  );\n}\n\n/* Progress fill - darker grey to show completion */\n.sidecar-status-bar-progress {\n  position: absolute;\n  top: 0;\n  left: 0;\n  height: 100%;\n  background: rgba(11, 18, 32, 0.35);\n  border-radius: 2px;\n  transition: width 0.3s ease;\n}\n\n/* Current position marker */\n.sidecar-status-bar-marker {\n  position: absolute;\n  top: -3px;\n  width: 9px;\n  height: 9px;\n  border-radius: 50%;\n  transform: translateX(-50%);\n  background: #0b1220;\n  border: 2px solid #ffffff;\n  box-shadow: 0 1px 3px rgba(2, 6, 23, 0.2);\n  z-index: 2;\n}\n\n/* Percentage label */\n.sidecar-status-bar-percent {\n  font-size: 9px;\n  font-weight: 600;\n  color: #0b1220;\n  white-space: nowrap;\n}\n\n/* Bottleneck Card - Core UI */\n/* Mobius Greeting */\n.sidecar-greeting {\n  display: flex;\n  flex-direction: column;\n  gap: 2px;\n  padding-bottom: 8px;\n  margin-bottom: 6px;\n  border-bottom: 1px solid rgba(11, 18, 32, 0.06);\n}\n\n.sidecar-greeting-text {\n  font-size: 12px;\n  font-weight: 500;\n  color: #0b1220;\n}\n\n.sidecar-greeting-subtext {\n  font-size: 10px;\n  font-weight: 400;\n  color: rgba(91, 102, 122, 0.6);\n}\n\n.sidecar-bottleneck-card {\n  padding: 0;\n  display: flex;\n  flex-direction: column;\n  gap: 8px;\n}\n\n.sidecar-bottleneck-card--clear {\n  padding: 6px 0;\n}\n\n.sidecar-bottleneck-header {\n  display: flex;\n  align-items: center;\n  justify-content: space-between;\n  padding-bottom: 6px;\n  border-bottom: 1px solid rgba(11, 18, 32, 0.08);\n}\n\n.sidecar-bottleneck-count {\n  font-size: 11px;\n  font-weight: 500;\n  color: #0b1220;\n}\n\n.sidecar-bottleneck-hint {\n  font-size: 9px;\n  font-weight: 400;\n  color: rgba(91, 102, 122, 0.6);\n}\n\n.sidecar-bottleneck-item {\n  background: #ffffff;\n  border: 1px solid rgba(11, 18, 32, 0.08);\n  border-radius: 6px;\n  padding: 6px 8px;\n  display: flex;\n  box-shadow: none;\n  flex-direction: column;\n  gap: 4px;\n}\n\n.sidecar-bottleneck-question-row {\n  display: flex;\n  align-items: flex-start;\n  gap: 4px;\n}\n\n.sidecar-bottleneck-question-row::before {\n  content: '';\n  display: inline-block;\n  width: 4px;\n  height: 4px;\n  min-width: 4px;\n  background: #3b82f6;\n  border-radius: 50%;\n  margin-top: 4px;\n  flex-shrink: 0;\n}\n\n.sidecar-bottleneck-checkbox {\n  width: 14px;\n  height: 14px;\n  cursor: pointer;\n  margin-top: 1px;\n}\n\n.sidecar-bottleneck-question {\n  flex: 1;\n  font-size: 10px;\n  font-weight: 500;\n  color: #0b1220;\n  line-height: 1.25;\n}\n\n.sidecar-source-badge {\n  font-size: 10px;\n  opacity: 0.7;\n}\n\n.sidecar-conflict-badge {\n  font-size: 12px;\n}\n\n.sidecar-bottleneck-answers {\n  display: flex;\n  gap: 10px;\n  flex-wrap: wrap;\n  margin-top: 4px;\n  padding: 4px 0;\n  align-items: center;\n  border-top: 1px solid rgba(11, 18, 32, 0.04);\n}\n\n#mobius-os-sidebar .sidecar-bottleneck-answers button.sidecar-answer-btn,\n.sidecar-answer-btn {\n  /* Reset all button defaults with high specificity */\n  all: unset !important;\n  box-sizing: border-box !important;\n  /* Apply our styles - pill buttons with grey background */\n  display: inline-block !important;\n  padding: 3px 8px !important;\n  margin: 0 !important;\n  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, sans-serif !important;\n  font-size: 9px !important;\n  font-weight: 400 !important;\n  background: rgba(11, 18, 32, 0.06) !important;\n  background-color: rgba(11, 18, 32, 0.06) !important;\n  border: 1px solid rgba(11, 18, 32, 0.08) !important;\n  border-radius: 4px !important;\n  color: rgba(91, 102, 122, 0.85) !important;\n  cursor: pointer !important;\n  transition: all 0.15s ease !important;\n  text-decoration: none !important;\n  line-height: 1.3 !important;\n  white-space: nowrap !important;\n  box-shadow: none !important;\n  outline: none !important;\n}\n\n#mobius-os-sidebar .sidecar-bottleneck-answers button.sidecar-answer-btn:hover,\n.sidecar-answer-btn:hover {\n  background: rgba(11, 18, 32, 0.1) !important;\n  background-color: rgba(11, 18, 32, 0.1) !important;\n  border-color: rgba(11, 18, 32, 0.15) !important;\n  color: #0b1220 !important;\n}\n\n#mobius-os-sidebar .sidecar-bottleneck-answers button.sidecar-answer-btn.sidecar-answer-btn--selected,\n#mobius-os-sidebar button.sidecar-answer-btn.sidecar-answer-btn--selected,\n.sidecar-bottleneck-answers .sidecar-answer-btn.sidecar-answer-btn--selected,\n.sidecar-answer-btn.sidecar-answer-btn--selected,\n.sidecar-answer-btn--selected {\n  background: rgba(37, 99, 235, 0.15) !important;\n  background-color: rgba(37, 99, 235, 0.15) !important;\n  border: 1px solid rgba(37, 99, 235, 0.35) !important;\n  border-color: rgba(37, 99, 235, 0.35) !important;\n  color: #2563eb !important;\n  font-weight: 500 !important;\n}\n\n/* Assignment Dropdown - compact, near question */\n.sidecar-assign-dropdown {\n  position: relative;\n  flex-shrink: 0;\n  margin-left: auto;\n}\n\n.sidecar-assign-btn {\n  display: flex;\n  align-items: center;\n  gap: 3px;\n  padding: 3px 6px;\n  font-size: 9px;\n  font-weight: 400;\n  color: rgba(37, 99, 235, 0.9);\n  background: rgba(37, 99, 235, 0.06);\n  border: 1px solid rgba(37, 99, 235, 0.15);\n  border-radius: 6px;\n  cursor: pointer;\n  transition: all 0.15s ease;\n}\n\n.sidecar-assign-btn:hover {\n  background: rgba(37, 99, 235, 0.12);\n  border-color: rgba(37, 99, 235, 0.25);\n}\n\n.sidecar-assign-btn--assigned {\n  background: rgba(34, 197, 94, 0.08);\n  border-color: rgba(34, 197, 94, 0.25);\n  color: rgba(34, 197, 94, 0.9);\n}\n\n.sidecar-assign-label {\n  font-size: 9px;\n}\n\n.sidecar-assign-arrow {\n  font-size: 8px;\n  opacity: 0.6;\n}\n\n.sidecar-assign-menu {\n  position: absolute;\n  top: 100%;\n  right: 0;\n  margin-top: 4px;\n  min-width: 140px;\n  background: #ffffff;\n  border: 1px solid rgba(11, 18, 32, 0.10);\n  border-radius: 8px;\n  box-shadow: 0 4px 16px rgba(2, 6, 23, 0.10);\n  z-index: 100;\n  overflow: hidden;\n  padding: 4px 0;\n}\n\n.sidecar-assign-option {\n  display: flex;\n  flex-direction: column;\n  align-items: flex-start;\n  gap: 1px;\n  width: 100%;\n  padding: 6px 10px;\n  font-size: 9px;\n  font-weight: 400;\n  color: #0b1220;\n  background: transparent;\n  border: none;\n  cursor: pointer;\n  text-align: left;\n}\n\n.sidecar-assign-option:hover {\n  background: rgba(37, 99, 235, 0.06);\n}\n\n.sidecar-assign-option-text {\n  font-size: 10px;\n  font-weight: 400;\n  color: #0b1220;\n}\n\n.sidecar-assign-option-hint {\n  font-size: 8px;\n  font-weight: 400;\n  color: rgba(91, 102, 122, 0.6);\n}\n\n.sidecar-assign-divider {\n  height: 1px;\n  background: rgba(11, 18, 32, 0.08);\n  margin: 4px 0;\n}\n\n/* Legacy delegate row - hidden but kept for compatibility */\n.sidecar-bottleneck-delegate {\n  display: none;\n}\n\n.sidecar-bottleneck-separator {\n  font-size: 9px;\n  color: rgba(91, 102, 122, 0.7);\n  text-align: center;\n}\n\n.sidecar-mode-btn {\n  padding: 3px 8px;\n  font-size: 9px;\n  font-weight: 500;\n  border-radius: 8px;\n  cursor: pointer;\n  transition: all 0.15s ease;\n  border: 1px solid rgba(11, 18, 32, 0.12);\n  background: rgba(11, 18, 32, 0.03);\n  color: #0b1220;\n}\n\n.sidecar-mode-btn--mobius {\n  background: rgba(11, 18, 32, 0.03);\n}\n\n.sidecar-mode-btn--mobius:hover {\n  background: rgba(11, 18, 32, 0.06);\n}\n\n.sidecar-mode-btn--user {\n  background: rgba(11, 18, 32, 0.03);\n}\n\n.sidecar-mode-btn--user:hover {\n  background: rgba(11, 18, 32, 0.06);\n}\n\n.sidecar-mode-btn--selected {\n  background: rgba(37, 99, 235, 0.1);\n  border-color: rgba(37, 99, 235, 0.35);\n  color: #2563eb;\n}\n\n/* Mobius tip - encourages user adoption */\n.sidecar-bottleneck-mobius-tip {\n  display: flex;\n  align-items: center;\n  gap: 4px;\n  margin-top: 4px;\n  padding: 3px 6px;\n  font-size: 8px;\n  font-weight: 400;\n  color: rgba(37, 99, 235, 0.75);\n  background: rgba(37, 99, 235, 0.04);\n  border-radius: 4px;\n}\n\n.sidecar-bottleneck-mobius-tip:hover {\n  background: rgba(37, 99, 235, 0.08);\n  color: #2563eb;\n}\n\n.sidecar-mobius-tip-icon {\n  font-size: 9px;\n}\n\n.sidecar-bottleneck-mobius-tip--assigned {\n  color: rgba(34, 197, 94, 0.85);\n  background: rgba(34, 197, 94, 0.06);\n  cursor: default;\n}\n\n.sidecar-bottleneck-mobius-tip--assigned:hover {\n  background: rgba(34, 197, 94, 0.06);\n  color: rgba(34, 197, 94, 0.85);\n}\n\n/* Inline note row - matches Mini note styling */\n.sidecar-bottleneck-note-row {\n  margin-top: 4px;\n  padding-top: 4px;\n  border-top: 1px solid rgba(11, 18, 32, 0.06);\n}\n\n.sidecar-bottleneck-note-wrap {\n  width: 100%;\n  position: relative;\n  display: flex;\n  align-items: center;\n}\n\n.sidecar-bottleneck-note-input {\n  width: 100%;\n  height: 22px;\n  border-radius: 6px;\n  border: 1px solid rgba(11, 18, 32, 0.12);\n  background: #fff;\n  padding: 0 26px 0 6px;\n  font-size: 9px;\n  font-weight: 400;\n  color: #0b1220;\n  outline: none;\n}\n\n.sidecar-bottleneck-note-input::placeholder {\n  color: rgba(91, 102, 122, 0.75);\n  font-weight: 500;\n}\n\n.sidecar-bottleneck-note-input:focus {\n  border-color: rgba(37, 99, 235, 0.55);\n}\n\n.sidecar-bottleneck-note-send {\n  position: absolute;\n  right: 0;\n  width: 22px;\n  height: 22px;\n  border-radius: 6px;\n  border: 1px solid rgba(11, 18, 32, 0.12);\n  background: rgba(11, 18, 32, 0.03);\n  cursor: pointer;\n  display: grid;\n  place-items: center;\n}\n\n.sidecar-bottleneck-note-send:hover {\n  background: rgba(11, 18, 32, 0.06);\n}\n\n.sidecar-bottleneck-note-send svg {\n  width: 14px;\n  height: 14px;\n  display: block;\n  fill: rgba(11, 18, 32, 0.85);\n}\n\n/* Inline button (Edit, small actions) */\n.sidecar-inline-btn {\n  height: 22px;\n  padding: 0 8px;\n  border-radius: 8px;\n  border: 1px solid rgba(11, 18, 32, 0.12);\n  background: rgba(11, 18, 32, 0.03);\n  cursor: pointer;\n  font-size: 9px;\n  font-weight: 600;\n  color: #0b1220;\n}\n\n.sidecar-inline-btn:hover {\n  background: rgba(11, 18, 32, 0.06);\n}\n\n/* Icon-only button (pencil edit, etc) - matches Mini */\n.sidecar-icon-btn {\n  width: 22px;\n  height: 22px;\n  border-radius: 8px;\n  border: 1px solid rgba(11, 18, 32, 0.12);\n  background: rgba(11, 18, 32, 0.03);\n  cursor: pointer;\n  display: grid;\n  place-items: center;\n  padding: 0;\n}\n\n.sidecar-icon-btn:hover {\n  background: rgba(11, 18, 32, 0.06);\n}\n\n.sidecar-icon-btn svg {\n  width: 12px;\n  height: 12px;\n  fill: rgba(11, 18, 32, 0.80);\n}\n\n.sidecar-bottleneck-bulk {\n  padding-top: 12px;\n  border-top: 1px solid #e5e7eb;\n}\n\n.sidecar-bottleneck-bulk-btn {\n  width: 100%;\n  padding: 8px 12px;\n  font-size: 10px;\n  font-weight: 600;\n  background: rgba(11, 18, 32, 0.03);\n  color: #0b1220;\n  border: 1px solid rgba(11, 18, 32, 0.12);\n  border-radius: 10px;\n  cursor: pointer;\n  transition: all 0.15s ease;\n}\n\n.sidecar-bottleneck-bulk-btn:hover:not(:disabled) {\n  background: rgba(11, 18, 32, 0.06);\n}\n\n.sidecar-bottleneck-bulk-btn:disabled {\n  opacity: 0.5;\n  cursor: not-allowed;\n}\n\n.sidecar-bottleneck-clear {\n  display: flex;\n  align-items: center;\n  gap: 10px;\n  padding: 10px 12px;\n  background: rgba(34, 197, 94, 0.08);\n  border: 1px solid rgba(34, 197, 94, 0.15);\n  border-radius: 10px;\n}\n\n.sidecar-bottleneck-clear-icon {\n  width: 24px;\n  height: 24px;\n  background: #22c55e;\n  color: white;\n  border-radius: 50%;\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  font-size: 12px;\n  font-weight: bold;\n}\n\n.sidecar-bottleneck-clear-text {\n  font-size: 11px;\n  color: #166534;\n  font-weight: 500;\n}\n\n/* Context Expander */\n/* Context Expander - matches Mini row styling */\n.sidecar-context-expander {\n  border-top: 1px solid rgba(11, 18, 32, 0.08);\n  margin-top: 8px;\n}\n\n.sidecar-context-expander-toggle {\n  width: 100%;\n  padding: 6px 0;\n  background: none;\n  border: none;\n  display: flex;\n  align-items: center;\n  gap: 4px;\n  cursor: pointer;\n  font-size: 10px;\n  font-weight: 400;\n  color: rgba(91, 102, 122, 0.95);\n  transition: background 0.15s ease;\n}\n\n.sidecar-context-expander-toggle:hover {\n  background: rgba(11, 18, 32, 0.02);\n}\n\n.sidecar-context-expander-arrow {\n  font-size: 8px;\n  color: rgba(91, 102, 122, 0.4);\n  transition: transform 0.15s ease;\n}\n\n.sidecar-context-expander-toggle[aria-expanded=\"true\"] .sidecar-context-expander-arrow {\n  transform: none;\n}\n\n.sidecar-context-expander-label {\n  font-weight: 400;\n  color: rgba(91, 102, 122, 0.7);\n}\n\n.sidecar-context-expander-content {\n  padding: 0 0 8px;\n}\n\n.sidecar-section-title {\n  font-size: 7px;\n  font-weight: 500;\n  color: rgba(91, 102, 122, 0.6);\n  text-transform: uppercase;\n  letter-spacing: 0.04em;\n  margin-bottom: 4px;\n}\n\n/* Resolution Plan Section */\n.sidecar-plan-section {\n  margin-bottom: 10px;\n  padding-bottom: 8px;\n  border-bottom: 1px solid rgba(11, 18, 32, 0.06);\n}\n\n.sidecar-plan-summary {\n  margin-bottom: 6px;\n}\n\n.sidecar-plan-factors {\n  display: flex;\n  flex-direction: column;\n  gap: 4px;\n}\n\n.sidecar-plan-factor {\n  display: flex;\n  align-items: center;\n  gap: 6px;\n  font-size: 9px;\n}\n\n.sidecar-plan-factor-label {\n  flex: 1;\n  color: rgba(91, 102, 122, 0.85);\n  font-weight: 400;\n}\n\n.sidecar-plan-factor-progress {\n  font-size: 8px;\n  color: rgba(91, 102, 122, 0.6);\n  font-weight: 400;\n}\n\n.sidecar-plan-factor-bar {\n  width: 40px;\n  height: 3px;\n  background: rgba(11, 18, 32, 0.08);\n  border-radius: 2px;\n  overflow: hidden;\n}\n\n.sidecar-plan-factor-fill {\n  height: 100%;\n  background: rgba(11, 18, 32, 0.35);\n  border-radius: 2px;\n  transition: width 0.3s ease;\n}\n\n.sidecar-plan-current {\n  display: flex;\n  align-items: flex-start;\n  gap: 4px;\n  padding: 4px 6px;\n  background: rgba(11, 18, 32, 0.03);\n  border-radius: 6px;\n  margin-top: 6px;\n}\n\n.sidecar-plan-current-label {\n  font-size: 8px;\n  font-weight: 400;\n  color: rgba(91, 102, 122, 0.6);\n  flex-shrink: 0;\n}\n\n.sidecar-plan-current-text {\n  font-size: 9px;\n  font-weight: 400;\n  color: rgba(91, 102, 122, 0.85);\n  line-height: 1.3;\n}\n\n.sidecar-plan-actions {\n  font-size: 8px;\n  font-weight: 400;\n  color: rgba(37, 99, 235, 0.8);\n  margin-top: 4px;\n}\n\n/* Milestones - matches Mini compact styling */\n.sidecar-milestones-list {\n  display: flex;\n  flex-direction: column;\n  gap: 4px;\n}\n\n.sidecar-milestone {\n  background: rgba(11, 18, 32, 0.02);\n  border: 1px solid rgba(11, 18, 32, 0.06);\n  border-radius: 8px;\n  overflow: hidden;\n}\n\n.sidecar-milestone-header {\n  display: flex;\n  align-items: center;\n  gap: 6px;\n  padding: 6px 10px;\n}\n\n.sidecar-milestone-icon {\n  width: 20px;\n  height: 20px;\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  font-size: 12px;\n}\n\n.sidecar-milestone--complete .sidecar-milestone-icon {\n  color: #22c55e;\n}\n\n.sidecar-milestone--in_progress .sidecar-milestone-icon {\n  color: #f59e0b;\n}\n\n.sidecar-milestone--blocked .sidecar-milestone-icon {\n  color: #ef4444;\n}\n\n.sidecar-milestone--pending .sidecar-milestone-icon {\n  color: #9ca3af;\n}\n\n.sidecar-milestone-label {\n  flex: 1;\n  font-size: 13px;\n  color: #374151;\n}\n\n.sidecar-milestone-arrow {\n  font-size: 10px;\n  color: rgba(91, 102, 122, 0.7);\n}\n\n.sidecar-milestone-details {\n  padding: 8px 12px 12px 40px;\n  border-top: 1px solid #e5e7eb;\n}\n\n/* Substeps */\n.sidecar-substeps {\n  display: flex;\n  flex-direction: column;\n  gap: 4px;\n  margin-bottom: 8px;\n}\n\n.sidecar-substep {\n  display: flex;\n  align-items: center;\n  gap: 6px;\n  font-size: 12px;\n  color: #6b7280;\n}\n\n.sidecar-substep-icon {\n  font-size: 8px;\n}\n\n.sidecar-substep--complete .sidecar-substep-icon {\n  color: #22c55e;\n}\n\n.sidecar-substep--current .sidecar-substep-icon {\n  color: #2563eb;\n}\n\n/* Timeline */\n.sidecar-timeline {\n  border-left: 2px solid #e5e7eb;\n  padding-left: 12px;\n}\n\n.sidecar-timeline-title {\n  font-size: 11px;\n  font-weight: 600;\n  color: #9ca3af;\n  margin-bottom: 8px;\n}\n\n.sidecar-timeline-entry {\n  display: flex;\n  flex-wrap: wrap;\n  align-items: center;\n  gap: 6px;\n  font-size: 12px;\n  margin-bottom: 6px;\n  position: relative;\n}\n\n.sidecar-timeline-entry::before {\n  content: '';\n  position: absolute;\n  left: -16px;\n  width: 8px;\n  height: 8px;\n  background: #e5e7eb;\n  border-radius: 50%;\n}\n\n.sidecar-timeline-actor {\n  font-weight: 500;\n  color: #374151;\n}\n\n.sidecar-timeline-actor--mobius {\n  color: #2563eb;\n}\n\n.sidecar-timeline-actor--user {\n  color: #059669;\n}\n\n.sidecar-timeline-action {\n  color: #6b7280;\n}\n\n.sidecar-timeline-time {\n  color: #9ca3af;\n  font-size: 11px;\n}\n\n.sidecar-timeline-artifact {\n  color: #2563eb;\n  text-decoration: none;\n  font-size: 11px;\n}\n\n.sidecar-timeline-artifact:hover {\n  text-decoration: underline;\n}\n\n/* Payer Info */\n.sidecar-payer-section {\n  margin-top: 16px;\n  padding-top: 16px;\n  border-top: 1px solid #e5e7eb;\n}\n\n.sidecar-payer-info {\n  background: #f9fafb;\n  border-radius: 6px;\n  padding: 12px;\n}\n\n.sidecar-payer-name {\n  font-size: 14px;\n  font-weight: 600;\n  color: #1f2937;\n  margin-bottom: 8px;\n}\n\n.sidecar-payer-phone,\n.sidecar-payer-portal,\n.sidecar-payer-response-time {\n  font-size: 12px;\n  color: #6b7280;\n  margin-bottom: 4px;\n}\n\n.sidecar-payer-phone a,\n.sidecar-payer-portal a {\n  color: #2563eb;\n  text-decoration: none;\n}\n\n.sidecar-payer-phone a:hover,\n.sidecar-payer-portal a:hover {\n  text-decoration: underline;\n}\n\n.sidecar-policy-excerpts {\n  margin-top: 12px;\n}\n\n.sidecar-excerpts-title {\n  font-size: 11px;\n  font-weight: 600;\n  color: #9ca3af;\n  margin-bottom: 8px;\n}\n\n.sidecar-policy-excerpt {\n  background: white;\n  border: 1px solid #e5e7eb;\n  border-radius: 6px;\n  padding: 10px;\n  margin-bottom: 8px;\n}\n\n.sidecar-excerpt-topic {\n  font-size: 12px;\n  font-weight: 600;\n  color: #374151;\n  margin-bottom: 4px;\n}\n\n.sidecar-excerpt-content {\n  font-size: 12px;\n  color: #6b7280;\n  line-height: 1.4;\n}\n\n.sidecar-excerpt-source {\n  font-size: 10px;\n  color: #9ca3af;\n  margin-top: 4px;\n}\n\n/* Quick Chat */\n/* Quick Chat - matches Mini note input styling */\n.sidecar-quick-chat {\n  padding: 10px 10px;\n  border-top: 1px solid rgba(11, 18, 32, 0.08);\n  background: #ffffff;\n}\n\n.sidecar-quick-chat-wrapper {\n  display: flex;\n  align-items: center;\n  position: relative;\n}\n\n.sidecar-quick-chat-input {\n  flex: 1;\n  height: 28px;\n  padding: 0 32px 0 8px;\n  font-size: 9px;\n  font-weight: 400;\n  border: 1px solid rgba(11, 18, 32, 0.12);\n  border-radius: 6px;\n  outline: none;\n  color: #0b1220;\n  background: #fff;\n}\n\n.sidecar-quick-chat-input::placeholder {\n  color: rgba(91, 102, 122, 0.5);\n  font-weight: 400;\n}\n\n.sidecar-quick-chat-input:focus {\n  border-color: rgba(37, 99, 235, 0.35);\n}\n\n.sidecar-quick-chat-input:disabled {\n  background: rgba(11, 18, 32, 0.02);\n}\n\n.sidecar-quick-chat-send {\n  position: absolute;\n  right: 0;\n  width: 28px;\n  height: 28px;\n  background: transparent;\n  border: none;\n  border-radius: 6px;\n  font-size: 12px;\n  cursor: pointer;\n  transition: background 0.15s ease;\n  display: grid;\n  place-items: center;\n}\n\n.sidecar-quick-chat-send:hover:not(:disabled) {\n  background: rgba(11, 18, 32, 0.04);\n}\n\n.sidecar-quick-chat-send:disabled {\n  cursor: not-allowed;\n}\n\n.sidecar-quick-chat-send svg {\n  width: 14px;\n  height: 14px;\n  display: block;\n  fill: rgba(91, 102, 122, 0.6);\n}\n\n.sidecar-quick-chat-send:hover:not(:disabled) svg {\n  fill: rgba(91, 102, 122, 0.9);\n}\n\n.sidecar-quick-chat-send:disabled svg {\n  fill: rgba(11, 18, 32, 0.2);\n}\n\n.sidecar-quick-chat-hint {\n  font-size: 9px;\n  font-weight: 400;\n  color: rgba(91, 102, 122, 0.5);\n  margin-top: 6px;\n}\n\n.sidecar-quick-chat-response {\n  background: #f0f9ff;\n  border: 1px solid #bae6fd;\n  border-radius: 8px;\n  padding: 12px;\n  margin-bottom: 8px;\n  position: relative;\n}\n\n.sidecar-quick-chat-response-text {\n  font-size: 13px;\n  color: #1e3a5f;\n  line-height: 1.4;\n}\n\n.sidecar-quick-chat-response-source {\n  font-size: 11px;\n  color: #6b7280;\n  margin-top: 6px;\n}\n\n.sidecar-quick-chat-response-dismiss {\n  position: absolute;\n  top: 8px;\n  right: 8px;\n  background: none;\n  border: none;\n  color: #9ca3af;\n  cursor: pointer;\n  font-size: 16px;\n}\n\n/* Sidecar Menu */\n/* Sidecar Menu - matches Mini menu styling */\n.sidecar-menu {\n  position: relative;\n}\n\n.sidecar-menu-btn {\n  width: 22px;\n  height: 22px;\n  border-radius: 6px;\n  border: 1px solid rgba(11, 18, 32, 0.12);\n  background: rgba(11, 18, 32, 0.03);\n  cursor: pointer;\n  display: grid;\n  place-items: center;\n  padding: 0;\n}\n\n.sidecar-menu-btn svg {\n  width: 14px;\n  height: 14px;\n  fill: rgba(11, 18, 32, 0.7);\n}\n\n.sidecar-menu-btn:hover {\n  background: rgba(11, 18, 32, 0.06);\n}\n\n.sidecar-menu-dropdown {\n  position: absolute;\n  top: 100%;\n  right: 0;\n  margin-top: 4px;\n  min-width: 140px;\n  background: #fff;\n  border: 1px solid rgba(11, 18, 32, 0.12);\n  border-radius: 10px;\n  box-shadow: 0 8px 24px rgba(2, 6, 23, 0.12);\n  z-index: 2147483647;\n  overflow: visible;\n  pointer-events: auto;\n  padding: 4px 0;\n}\n\n.sidecar-menu-item {\n  display: flex;\n  align-items: center;\n  gap: 8px;\n  width: 100%;\n  text-align: left;\n  padding: 8px 12px;\n  border: none;\n  background: transparent;\n  font-size: 11px;\n  font-weight: 400;\n  color: #0b1220;\n  cursor: pointer;\n  pointer-events: auto;\n  transition: background 0.15s ease;\n  position: relative;\n}\n\n.sidecar-menu-item:hover {\n  background: rgba(11, 18, 32, 0.04);\n}\n\n.sidecar-menu-item.danger {\n  color: #b3261e;\n}\n\n.sidecar-menu-icon {\n  width: 14px;\n  height: 14px;\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  flex-shrink: 0;\n}\n\n.sidecar-menu-icon svg {\n  width: 14px;\n  height: 14px;\n  fill: rgba(11, 18, 32, 0.6);\n}\n\n.sidecar-menu-label {\n  flex: 1;\n  font-size: 11px;\n  font-weight: 400;\n}\n\n.sidecar-menu-arrow {\n  font-size: 10px;\n  color: rgba(91, 102, 122, 0.6);\n  margin-left: auto;\n}\n\n.sidecar-menu-divider {\n  height: 1px;\n  background: rgba(11, 18, 32, 0.08);\n  margin: 4px 0;\n}\n\n/* Theme submenu - positioned to the left since sidecar is on right edge */\n.sidecar-menu-submenu {\n  position: absolute;\n  right: 100%;\n  left: auto;\n  top: 0;\n  margin-right: 4px;\n  min-width: 100px;\n  background: #fff;\n  border: 1px solid rgba(11, 18, 32, 0.12);\n  border-radius: 8px;\n  box-shadow: 0 4px 12px rgba(2, 6, 23, 0.1);\n  padding: 4px 0;\n  z-index: 2147483648;\n}\n\n.sidecar-menu-submenu-item {\n  display: flex;\n  align-items: center;\n  gap: 6px;\n  padding: 6px 10px;\n  font-size: 10px;\n  font-weight: 400;\n  color: #0b1220;\n  cursor: pointer;\n  transition: background 0.15s ease;\n}\n\n.sidecar-menu-submenu-item:hover {\n  background: rgba(11, 18, 32, 0.04);\n}\n\n.sidecar-menu-theme-icon {\n  font-size: 8px;\n  color: rgba(91, 102, 122, 0.7);\n}\n\n.sidecar-menu-theme-label {\n  font-size: 10px;\n  font-weight: 400;\n}\n\n/* Toggle switch - matches Mini style */\n.sidecar-menu-toggle {\n  position: relative;\n  width: 32px;\n  height: 18px;\n}\n\n.sidecar-menu-toggle-input {\n  opacity: 0;\n  width: 0;\n  height: 0;\n}\n\n.sidecar-menu-toggle-slider {\n  position: absolute;\n  cursor: pointer;\n  top: 0;\n  left: 0;\n  right: 0;\n  bottom: 0;\n  background: rgba(11, 18, 32, 0.15);\n  border-radius: 9px;\n  transition: background 0.2s ease;\n}\n\n.sidecar-menu-toggle-slider::before {\n  content: '';\n  position: absolute;\n  height: 14px;\n  width: 14px;\n  left: 2px;\n  bottom: 2px;\n  background: white;\n  border-radius: 50%;\n  transition: transform 0.2s ease;\n  box-shadow: 0 1px 3px rgba(2, 6, 23, 0.15);\n}\n\n.sidecar-menu-toggle-input:checked + .sidecar-menu-toggle-slider {\n  background: #2563eb;\n}\n\n.sidecar-menu-toggle-input:checked + .sidecar-menu-toggle-slider::before {\n  transform: translateX(14px);\n}\n\n/* Collapse button - matches Mini expand/minimize buttons */\n.sidecar-collapse-btn {\n  width: 22px;\n  height: 22px;\n  border-radius: 6px;\n  border: 1px solid rgba(11, 18, 32, 0.12);\n  background: rgba(11, 18, 32, 0.03);\n  cursor: pointer;\n  display: grid;\n  place-items: center;\n  padding: 0;\n}\n\n.sidecar-collapse-btn svg {\n  width: 14px;\n  height: 14px;\n  fill: rgba(11, 18, 32, 0.7);\n}\n\n.sidecar-collapse-btn:hover {\n  background: rgba(11, 18, 32, 0.06);\n}\n\n/* Alert Indicator */\n/* Alert Indicator - matches Mini button styling */\n.sidecar-alert-indicator {\n  position: relative;\n  width: 22px;\n  height: 22px;\n  border-radius: 6px;\n  border: 1px solid rgba(11, 18, 32, 0.12);\n  background: rgba(11, 18, 32, 0.03);\n  cursor: pointer;\n  display: grid;\n  place-items: center;\n}\n\n.sidecar-alert-indicator:hover {\n  background: rgba(11, 18, 32, 0.06);\n}\n\n.sidecar-alert-icon {\n  width: 14px;\n  height: 14px;\n  display: inline-flex;\n  align-items: center;\n  justify-content: center;\n}\n\n.sidecar-alert-icon svg {\n  width: 14px;\n  height: 14px;\n  display: block;\n  fill: none;\n  stroke: rgba(11, 18, 32, 0.85);\n  stroke-width: 1.6;\n  stroke-linecap: round;\n  stroke-linejoin: round;\n}\n\n.sidecar-alert-badge {\n  position: absolute;\n  top: -4px;\n  right: -4px;\n  min-width: 14px;\n  height: 14px;\n  background: #2563eb;\n  color: white;\n  font-size: 9px;\n  font-weight: 600;\n  border-radius: 7px;\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  padding: 0 3px;\n}\n\n.sidecar-alert-badge--high {\n  background: #dc2626;\n}\n\n.sidecar-notification-dot {\n  width: 6px;\n  height: 6px;\n  background: #2563eb;\n  border-radius: 50%;\n}\n\n.sidecar-notification-dot--high {\n  background: #dc2626;\n}\n\n/* Privacy indicator - matches Mini style */\n.sidecar-privacy-indicator {\n  font-size: 11px;\n  color: rgba(91, 102, 122, 0.95);\n}\n\n/* ========================================\n   SIDECAR THEMES\n   ======================================== */\n\n/* Dark Theme */\n#mobius-os-sidebar.theme-dark {\n  background: #1a1f2e;\n  color: #e5e7eb;\n  border-left-color: rgba(255, 255, 255, 0.08);\n}\n\n#mobius-os-sidebar.theme-dark .top-row {\n  background: rgba(255, 255, 255, 0.02);\n  border-bottom-color: rgba(255, 255, 255, 0.06);\n}\n\n#mobius-os-sidebar.theme-dark .sidecar-status-bar {\n  background: #1a1f2e;\n  border-bottom-color: rgba(255, 255, 255, 0.06);\n}\n\n#mobius-os-sidebar.theme-dark .sidecar-status-bar-context {\n  color: rgba(229, 231, 235, 0.8);\n}\n\n#mobius-os-sidebar.theme-dark .sidecar-status-bar-track {\n  background: linear-gradient(90deg, \n    rgba(255, 255, 255, 0.06) 0%, \n    rgba(255, 255, 255, 0.1) 50%,\n    rgba(255, 255, 255, 0.04) 100%\n  );\n}\n\n#mobius-os-sidebar.theme-dark .sidecar-status-bar-progress {\n  background: rgba(255, 255, 255, 0.3);\n}\n\n#mobius-os-sidebar.theme-dark .sidecar-status-bar-marker {\n  background: #e5e7eb;\n  border-color: #1a1f2e;\n}\n\n#mobius-os-sidebar.theme-dark .sidecar-status-bar-percent {\n  color: #e5e7eb;\n}\n\n#mobius-os-sidebar.theme-dark .sidecar-bottleneck-item {\n  background: rgba(255, 255, 255, 0.04);\n  border-color: rgba(255, 255, 255, 0.08);\n}\n\n#mobius-os-sidebar.theme-dark .sidecar-bottleneck-question {\n  color: #e5e7eb;\n}\n\n/* Dark theme answer buttons moved to complete dark theme section below */\n\n#mobius-os-sidebar.theme-dark .sidecar-menu-btn,\n#mobius-os-sidebar.theme-dark .sidecar-collapse-btn,\n#mobius-os-sidebar.theme-dark .sidecar-alert-indicator {\n  background: rgba(255, 255, 255, 0.06);\n  border-color: rgba(255, 255, 255, 0.12);\n}\n\n#mobius-os-sidebar.theme-dark .sidecar-menu-btn svg,\n#mobius-os-sidebar.theme-dark .sidecar-collapse-btn svg,\n#mobius-os-sidebar.theme-dark .sidecar-alert-icon svg {\n  fill: rgba(229, 231, 235, 0.8);\n}\n\n#mobius-os-sidebar.theme-dark .sidecar-menu-dropdown,\n#mobius-os-sidebar.theme-dark .sidecar-menu-submenu {\n  background: #252b3b;\n  border-color: rgba(255, 255, 255, 0.1);\n}\n\n#mobius-os-sidebar.theme-dark .sidecar-menu-item {\n  color: #e5e7eb;\n}\n\n#mobius-os-sidebar.theme-dark .sidecar-menu-item:hover {\n  background: rgba(255, 255, 255, 0.06);\n}\n\n#mobius-os-sidebar.theme-dark .sidecar-menu-icon svg {\n  fill: rgba(229, 231, 235, 0.6);\n}\n\n#mobius-os-sidebar.theme-dark .sidecar-quick-chat {\n  background: rgba(255, 255, 255, 0.02);\n  border-top-color: rgba(255, 255, 255, 0.06);\n}\n\n#mobius-os-sidebar.theme-dark .sidecar-quick-chat-input {\n  background: rgba(255, 255, 255, 0.06);\n  border-color: rgba(255, 255, 255, 0.12);\n  color: #e5e7eb;\n}\n\n#mobius-os-sidebar.theme-dark .sidecar-quick-chat-input::placeholder {\n  color: rgba(229, 231, 235, 0.5);\n}\n\n#mobius-os-sidebar.theme-dark .sidecar-quick-chat-send {\n  background: rgba(255, 255, 255, 0.06);\n  border-color: rgba(255, 255, 255, 0.12);\n}\n\n#mobius-os-sidebar.theme-dark .sidecar-quick-chat-send svg {\n  fill: rgba(229, 231, 235, 0.7);\n}\n\n#mobius-os-sidebar.theme-dark .sidecar-section-title {\n  color: rgba(229, 231, 235, 0.7);\n}\n\n#mobius-os-sidebar.theme-dark .sidecar-context-expander-toggle {\n  color: #e5e7eb;\n}\n\n#mobius-os-sidebar.theme-dark .sidecar-context-expander-toggle:hover {\n  background: rgba(255, 255, 255, 0.04);\n}\n\n/* Glass Theme */\n#mobius-os-sidebar.theme-glass {\n  background: rgba(255, 255, 255, 0.75);\n  backdrop-filter: blur(20px);\n  -webkit-backdrop-filter: blur(20px);\n  border-left-color: rgba(255, 255, 255, 0.3);\n}\n\n#mobius-os-sidebar.theme-glass .top-row {\n  background: rgba(255, 255, 255, 0.4);\n}\n\n#mobius-os-sidebar.theme-glass .sidecar-status-bar {\n  background: rgba(255, 255, 255, 0.5);\n}\n\n#mobius-os-sidebar.theme-glass .sidecar-bottleneck-item {\n  background: rgba(255, 255, 255, 0.6);\n  border-color: rgba(255, 255, 255, 0.4);\n  box-shadow: 0 2px 8px rgba(2, 6, 23, 0.06);\n}\n\n#mobius-os-sidebar.theme-glass .sidecar-menu-btn,\n#mobius-os-sidebar.theme-glass .sidecar-collapse-btn,\n#mobius-os-sidebar.theme-glass .sidecar-alert-indicator {\n  background: rgba(255, 255, 255, 0.5);\n  border-color: rgba(255, 255, 255, 0.4);\n}\n\n#mobius-os-sidebar.theme-glass .sidecar-menu-dropdown,\n#mobius-os-sidebar.theme-glass .sidecar-menu-submenu {\n  background: rgba(255, 255, 255, 0.9);\n  backdrop-filter: blur(10px);\n  -webkit-backdrop-filter: blur(10px);\n}\n\n#mobius-os-sidebar.theme-glass .sidecar-quick-chat {\n  background: rgba(255, 255, 255, 0.4);\n}\n\n#mobius-os-sidebar.theme-glass .sidecar-quick-chat-input {\n  background: rgba(255, 255, 255, 0.7);\n}\n\n/* ============================================\n   COMPLETE DARK THEME - All Sidecar Elements\n   ============================================ */\n\n/* Dark: Greeting */\n#mobius-os-sidebar.theme-dark .sidecar-greeting {\n  border-bottom-color: rgba(255, 255, 255, 0.08);\n}\n\n#mobius-os-sidebar.theme-dark .sidecar-greeting-text {\n  color: #f1f5f9;\n}\n\n#mobius-os-sidebar.theme-dark .sidecar-greeting-subtext {\n  color: rgba(148, 163, 184, 0.7);\n}\n\n/* Dark: Patient Row */\n#mobius-os-sidebar.theme-dark .sidecar-patient-row {\n  border-bottom-color: rgba(255, 255, 255, 0.08) !important;\n}\n\n#mobius-os-sidebar.theme-dark .sidecar-patient-row label {\n  color: rgba(148, 163, 184, 0.8);\n}\n\n#mobius-os-sidebar.theme-dark .sidecar-patient-row span {\n  color: #f1f5f9;\n}\n\n#mobius-os-sidebar.theme-dark .sidecar-icon-btn {\n  background: rgba(255, 255, 255, 0.06);\n  border-color: rgba(255, 255, 255, 0.12);\n}\n\n#mobius-os-sidebar.theme-dark .sidecar-icon-btn svg {\n  fill: rgba(229, 231, 235, 0.7);\n}\n\n/* Dark: Status Bar Title */\n#mobius-os-sidebar.theme-dark .sidecar-status-bar-title {\n  color: rgba(148, 163, 184, 0.8);\n}\n\n/* Dark: Bottleneck Header */\n#